<script setup>
import { ref, onMounted  } from 'vue';

import DxSelectBox from 'devextreme-vue/select-box';
import DxTextBox from 'devextreme-vue/text-box'
import { CloseOutlined } from '@ant-design/icons-vue';

const props = defineProps(['row'])
const boxData = ref([])
const addActive = ref(true)
const deletedFields = ref([])
const memoBoxData = ref([])
const emit = defineEmits(['changePending']);

const type_value = {
    WORK: '',
    MOBILE: '',
    FAX: '',
    HOME: '',
    PAGER: '',
    MAILING: ' ',
    OTHER: '',
    OPENLINE: '-',
    IMOL: ' ',
}
const value_type = Object.fromEntries(Object.entries(type_value).map(a => a.reverse()))

onMounted(() => {
    boxData.value = _.cloneDeep(props.row.TRUEVALUE)||[]
    memoBoxData.value = _.cloneDeep(props.row.TRUEVALUE)||[]
})

const sendChanges = () => {
    const pending = []
    memoBoxData.value.forEach(val => {
        if (!boxData.value.filter(el => el.VALUE === val.VALUE && el.VALUE_TYPE === val.VALUE_TYPE && val.ID).length) {
            pending.push({
                ID: val.ID,
                VALUE: ''
            })
        }

    })
    boxData.value.forEach(val => {
        pending.push({ ...val })
    })
    let display
    if (pending.filter(contact => contact.VALUE).length) {
        display = pending.filter(contact => contact.VALUE)
            .map(contact => ({
                ID: type_value[contact.VALUE_TYPE],
                NAME: contact.VALUE
            }))
            .map(contact => `${contact.ID}: ${contact.NAME}, `)
            .reduce((a, b) => a.concat(b))
    } else display = ''

    emit('changePending', _.cloneDeep(pending),display)
}
const changeItemValue = (e, val) => {
    const key = val
    key ? boxData.value.find(elem => elem.ID === key).VALUE = e.event.currentTarget.value : boxData.value[boxData.value.length - 1].VALUE = e.event.currentTarget.value
    if (boxData.value[boxData.value.length - 1]?.VALUE && boxData.value[boxData.value.length - 1]?.VALUE_TYPE) {
        addActive.value = true
    }
    sendChanges()
}
const changeItemType = (e, val) => {
    const selected = value_type[e.selectedItem]
    const key = val
    key ? boxData.value.find(elem => elem.ID === key).VALUE_TYPE = selected : boxData.value[boxData.value.length - 1].VALUE_TYPE = selected
    if (boxData.value[boxData.value.length - 1].VALUE && boxData.value[boxData.value.length - 1].VALUE_TYPE) {
        addActive.value = true
    }
    sendChanges()
}
const deleteFieldInMulti = (e, id) => {
    const key = id;
    let item = boxData.value.find(item => item.ID === key);
    const index = boxData.value.indexOf(item)
    boxData.value.splice(index, 1)
    if (boxData.value.length) {
        if (boxData.value[boxData.value.length - 1] || boxData.value[boxData.value.length - 1].VALUE && boxData.value[boxData.value.length - 1].VALUE_TYPE) {
            addActive.value = true
            return
        }
    }
    addActive.value = true
    sendChanges()
    item && item.TYPE_ID && deletedFields.value.push({ ID: item.ID, VALUE_TYPE: item.VALUE_TYPE, VALUE: item.VALUE, TYPE_ID: item.TYPE_ID })
}
const addFieldInMulti = (e, id) => {
    if (boxData.value.length) {
        if (boxData.value[boxData.value.length - 1] || boxData.value[boxData.value.length - 1].VALUE && boxData.value[boxData.value.length - 1].VALUE_TYPE) {
            boxData.value.push(({ VALUE: '', TYPE_ID: '', VALUE_TYPE: '' }))
            addActive.value = false
            return
        }

    }
    boxData.value.push(({ VALUE: '', TYPE_ID: '', VALUE_TYPE: '' }))
    addActive.value = false
}
const clear = () =>{
    emit('changePending', '', '')
}
</script>
<template>
    <div style="overflow-y:visible">
        <div v-for="item in boxData"  style="padding:1rem; display:flex; flex-direction:row; align-items:center; "
            :key="item.ID">
            <DxTextBox v-if="item.VALUE||!item.ID" @input="changeItemValue($event, item.ID)" :value="item.VALUE" style="width:10rem"
                maskInvalidMessage="  " />
            <DxSelectBox v-if="item.VALUE||!item.ID"  @selection-changed="changeItemType($event, item.ID)" :data-source="Object.values(type_value)"
                :value="type_value[item.VALUE_TYPE]" style="width:8rem" />
            <a-button v-if="item.VALUE||!item.ID" style="border:0ch;  pointer-events:all; cursor:pointer;"
                @click="deleteFieldInMulti($event, item.ID)">
                <close-outlined style="pointer-events:none" />
            </a-button>
        </div>
        <a-button @click="addFieldInMulti" :disabled="!addActive"> </a-button>
    </div>
</template>