<script setup>
import { DxTextBox } from 'devextreme-vue';
import { ref } from 'vue';
const props = defineProps(['row'])
const emit = defineEmits(['changePending'])
const textArr = ref('')

const handleChange = (e) =>{
    textArr.value = e.event.currentTarget?.value
    emit('changePending', textArr.value, textArr.value)
}

</script>

<template>
<DxTextBox  @input="handleChange($event)" />


</template>