<script setup>
import { ref, computed, onBeforeMount } from 'vue';
import Marketplace from './screens/Marketplace.vue';
import TablescreenVue from './screens/Tablescreen.vue';
import { placement } from 'bx24-api';
const fixWindow = () =>{
  try {
    const q = window.name.split("|");
    const domain = q[0].replace(/:(80|443)$/, "");
    const protocol = parseInt(q[1]) && true || false;
    const app_sid = q[2];
    parent.postMessage("resizeWindow:" + JSON.stringify({
        "width": "100%",
        "height": Math.max(document.documentElement.scrollHeight, document.documentElement.offsetHeight)
    }) + "::" + app_sid, "http" + (protocol ? "s" : "") + "://" + domain);
    }
    catch (e) {
      console.error("resizeWindow", e);
    }
}
const place = ref({options:[], placement:'' });

const inFrame = computed(()=>!!window.name);
onBeforeMount(async()=>{
  if(inFrame.value) {
    setInterval(fixWindow, 500);
  }
  place.value = await placement.info()
  if(place.value.placement==='DEFAULT'){
    place.value = 'DEF'
  } else {
    place.value = 'NOTDEF'
  }
})

</script>

<template>
  <Marketplace v-if="place==='DEF'"/>
  <TablescreenVue v-if="place === 'NOTDEF'"/>
</template>

<style scoped>
.logo {
  height: 6em;
  padding: 1.5em;
  will-change: filter;
}
.logo:hover {
  filter: drop-shadow(0 0 2em #646cffaa);
}
.logo.vue:hover {
  filter: drop-shadow(0 0 2em #42b883aa);
}
</style>
