<script setup>
import { callMethod } from "bx24-api";
import DxDropDownBox from "devextreme-vue/drop-down-box";
import DxTreeView from "devextreme-vue/tree-view";
import { onBeforeMount, ref } from "vue";
import _ from "lodash";

const props = defineProps(["row", "valueRef"]);
const emit = defineEmits(["changePending"]);

const value = ref({});
const selectArray = ref({});
const entity = ref([]);
const row = ref({});
const truevalue = ref({
  lead: [],
  deal: [],
  contact: [],
  company: [],
  invoice: [],
});
const getEntityRu = (ent) => {
  console.log(ent);
  switch (ent) {
    case "LEAD":
      return "";
    case "DEAL":
      return "";
    case "COMPANY":
      return "";
    case "CONTACT":
      return "";
    case "SMART_INVOICE":
    case "INVOICE":
      return "";
  }
};
const getName = (entity, obj) => {
  switch (entity) {
    case "lead": {
      return `${obj.TITLE} [${obj.ID}]`;
    }
    case "deal":
      return `${obj.TITLE} [${obj.ID}]`;
    case "contact":
      return `${obj.NAME} [${obj.ID}]`;
    case "company":
      return `${obj.TITLE} [${obj.ID}]`;
    case "invoice":
      return `${obj.searchContent} [${obj.id}]`;
  }
};
onBeforeMount(() => {
  entity.value = _.cloneDeep(props.row.CRMENTITY);
  row.value = _.cloneDeep(props.row);
  props.row.TRUEVALUE.forEach(async(val) => {
    console.log(val);
    switch (val.substring(0, 2)) {
      case "L_": {
        const ID = val.substring(2);
        const NAME = await (await callMethod('crm.lead.get',{ID:ID})).data()['TITLE'];
        truevalue.value.lead.push({
            ID:ID,
            NAME: `${NAME} [${ID}]`
        })
        //return `: ${NAME} [${ID}], `;
        break
      }
      case "D_": {
        const ID = val.substring(2);
        const NAME = await (await callMethod('crm.deal.get',{ID:ID})).data()['TITLE'];
        truevalue.value.deal.push({
            ID:ID,
            NAME: `${NAME} [${ID}]`
        })
        break
      }
      case "C_": {
        const ID = val.substring(2);
        const NAME = await (await callMethod('crm.contact.get',{ID:ID})).data()['TITLE'];
        truevalue.value.contact.push({
            ID:ID,
            NAME: `${NAME} [${ID}]`
        })
        break
      }
      case "SI": {
        const ID = val.substring(3);
        const NAME = await (await callMethod('crm.item.get',{id:ID, entityTypeId:7})).data().item
          .searchContent;
          truevalue.value.invoice.push({
            ID:ID,
            NAME: `${NAME} [${ID}]`
        })
        break
      }
      case "CO": {
        const ID = val.substring(3);
        const NAME = await (await callMethod('crm.company.get',{ID:ID})).data()['TITLE'];
        truevalue.value.company.push({
            ID:ID,
            NAME: `${NAME} [${ID}]`
        })
        break
      }
    }
    // truevalue.value[val.ENTITY].push({
    //   ...val,
    //   NAME: `${val.NAME} [${val.ID}]`,
    // });
  });
  console.log(truevalue.value);
});
const handleOpen = async (e, item) => {
  
  let fetch = item;
  if (fetch === "smart_invoice") fetch = "invoice";
  const res =
    fetch !== "invoice"
      ? await (await callMethod(`crm.${fetch}.list`)).data()
      : await (await callMethod("crm.item.list", { entityTypeId: 7 })).data()
          .items;
  selectArray.value[fetch] = res
    .map((el) => ({
      ID: el.ID || el.id,
      NAME: getName(fetch, el),
    }))
    .map((el) => {
      if (truevalue.value[fetch].filter((val) => val.ID == el.ID).length) {
        return {
          ...el,
          CHECK: true,
        };
      } else
        return {
          ...el,
        };
    });
  // document.getElementsByClassName('dx-icon-clear')[0]
  //     .addEventListener('click', () => {
  //         selectArray.value = _.cloneDeep(cachedBox.value)
  //     })
};
const handleInput = async (e, item) => {
  let fetch = item;
  if (fetch === "smart_invoice") fetch = "invoice";
  console.log(fetch);
  console.log(e);
  const params = {
    FILTER: {
      "%TITLE": e.event.currentTarget.value,
    },
  };
  switch (fetch) {
    case "deal":
    case "company":
    case "contact":
    case "lead": {
      await (
        await callMethod(`crm.${fetch}.list`, params)
      )
        .data()
        .map((el) => ({
          ID: el.ID,
          NAME: `${el.TITLE} [${el.ID}]`,
        }))
        .map((el) => {
          if (row.value.TRUEVALUE.filter((val) => val.ID == el.ID).length) {
            return {
              ...el,
              CHECK: true,
            };
          } else
            return {
              ...el,
            };
        })
        .forEach((elem) => {
          console.log(elem);
          if (selectArray.value[fetch].filter((el) => el.ID == elem.ID).length)
            return;
          else selectArray.value[fetch] = [...selectArray.value[fetch], elem];
        });
      break;
    }
    case "invoice": {
      const invoiceparams = {
        entityTypeId: 7,
        filter: {
          "%searchContent": e.event.currentTarget.value,
        },
      };
      await (
        await callMethod(`crm.item.list`, invoiceparams)
      )
        .data()
        .items.map((el) => ({
          ID: el.id,
          NAME: `${el.searchContent} [${el.id}]`,
        }))
        .map((el) => {
          if (row.value.TRUEVALUE.filter((val) => val.ID == el.ID).length) {
            return {
              ...el,
              CHECK: true,
            };
          } else
            return {
              ...el,
            };
        })
        .forEach((elem) => {
          console.log(elem);
          if (selectArray.value[fetch].filter((el) => el.ID == elem.ID).length)
            return;
          else selectArray.value[fetch] = [...selectArray.value[fetch], elem];
        });
      break;
    }
  }
  console.log(selectArray.value[fetch]);
};
const treeChange = (e, entity) => {
  let ent = entity;
  if (entity === "smart_invoice") ent = "invoice";
  const item = e.itemData;
  if (item.CHECK) {
    truevalue.value[ent].push(item);
  } else {
    const elemToDel = truevalue.value[ent].find((el) => el.ID == item.ID);
    const indexToDel = truevalue.value[ent].indexOf(elemToDel);
    truevalue.value[ent].splice(indexToDel, 1);
  }
  let display;
  display = Object.keys(truevalue.value)
    .map((key) => {
      console.log(key);
      return truevalue.value[key]
        .map((elem) => `${getEntityRu(key.toUpperCase())}: ${elem.NAME}`)
        .join(", ");
    })
    .filter((el) => el)
    .join(", ");
  let pend = Object.keys(truevalue.value)
    .filter((key) => truevalue.value[key])
    .map((key) => {
      return truevalue.value[key].map((elem) => {
        console.log(elem);
        switch (key) {
          case "lead":
            return `L_${elem.ID}`;
          case "deal":
            return `D_${elem.ID}`;
          case "contact":
            return `C_${elem.ID}`;
          case "company":
            return `CO_${elem.ID}`;
          case "invoice":
            return `SI_${elem.ID}`;
        }
      });
    })
    .filter((el) => el.length)
    .flat();
  console.log(pend);
  emit("changePending", _.cloneDeep(pend), display);
};
const clear = () =>{
    emit('changePending', '', '')
}
</script>

<template>
  <a-button @click="clear">  </a-button>
  <br/>
  <div v-for="item in entity">
    <h3>{{ getEntityRu(item) }}</h3>
    <DxDropDownBox
      :data-source="selectArray[item.toLowerCase()]"
      value-expr="ID"
      display-expr="NAME"
      @opened="handleOpen($event, item.toLowerCase())"
    >
      <DxTreeView
        @opened="handleOpen($event, item.toLowerCase())"
        selected-expr="CHECK"
        :key="item"
        ref="drop"
        :value="value"
        data-structure="plain"
        selection-mode="multiple"
        show-check-boxes-mode="normal"
        @item-selection-changed="treeChange($event, item.toLowerCase())"
        :opened="true"
        :search-enabled="true"
        key-expr="ID"
        display-expr="NAME"
        search-expr="NAME"
        :data-source="
          selectArray[item === 'SMART_INVOICE' ? 'invoice' : item.toLowerCase()]
        "
        :search-editor-options="{
          onInput: async ($event) =>
            await handleInput($event, item.toLowerCase()),
        }"
      />
    </DxDropDownBox>
  </div>
</template>
