import { callBatch, callMethod } from "bx24-api"

export async function* getAdditional(data, fetch) {

    const userIDs = []
    const userfieldNames = []

    const companyIDS = []
    const companyFieldNames = []

    const iblockelIDs = []
    const iblockelfieldNames = []
    const iblockIDsForEl = []

    const iblocksecIDs = []
    const iblocksecfieldNames = []
    const iblockIDsForSec = []

    const statusesIDs = []
    const statusesfieldNames = []

    const crmIDs = []
    const crmfieldNames = []

    const contactIDs = []
    const contactfieldNames=[]

    Object.keys(fetch.value).forEach(async key => {
        switch (fetch.value[key].type) {
            case 'employee':
            case 'user': {
                userIDs.push(fetch.value[key].value)
                userfieldNames.push({ name: key, id: fetch.value[key].value })
                break;
            }
            case 'crm_company': {
                companyIDS.push(fetch.value[key].value)
                companyFieldNames.push({ name: key, id: fetch.value[key].value })
                break;
            }
            case 'iblock_element': {
                iblockelIDs.push(fetch.value[key].value)
                iblockIDsForEl.push(fetch.value[key].iblockid)
                iblockelfieldNames.push({ name: key, id: fetch.value[key].value, IBLOCK_ID: fetch.value[key].iblockid })
                break;
            }
            case 'crm_status': {
                statusesIDs.push(fetch.value[key].value)
                statusesfieldNames.push({name:key, id: fetch.value[key].value})
                break;
            }
            case 'crm': {
                console.log(fetch.value[key])
                crmIDs.push({id:fetch.value[key].value, entity:fetch.value[key].entity })
                crmfieldNames.push({name:key, id: fetch.value[key].value, entity:fetch.value[key].entity});
                break
            }
            case 'crm_contact': {
                contactIDs.push(fetch.value[key].value)
                contactfieldNames.push({ name: key, id: fetch.value[key].value })
                break;
            }

        }
    })

    const userSet = Array.from(new Set(userIDs)).flat()
    if (userSet.length) {
        const resUser = (await callMethod('user.get', { id: userSet })).data()
        const Users = resUser
        // .map(user => ({
        //     ID: user.ID,
        //     NAME: user.NAME ? `${user.NAME} ${user.LAST_NAME}` : `${user.EMAIL}`
        // }))
        const getName = (user) =>{
            if(user.NAME!=='') return `${user.NAME} ${user.LAST_NAME||''}, `
            else return `${user.EMAIL}, `
        }
        userfieldNames.forEach(field => {
            const row = data.value.filter(el => el.NAME === field.name)[0]
            if (Array.isArray(field.id) && field.id.length) {
                row.DISPLAYVALUE = Users.filter(el => field.id.includes(Number(el.ID)))
                .map((user) => getName(user)).reduce((a,b)=>a.concat(b))
                row.TRUEVALUE = Users.filter(el => field.id.includes(Number(el.ID)))
            } else if(field.id && field.id!=0){
                const row = data.value.filter(el => el.NAME === field.name)[0]
                row.DISPLAYVALUE = getName(Users.filter(user => user.ID === field.id)[0]) 
                row.TRUEVALUE = Users.filter(user => user.ID === field.id)[0]
            }
            row.ALLOW = true

        })
    }
    const companySet = Array.from(new Set(companyIDS)).flat()
    if (companySet.length) {
        const resUser = (await callMethod('crm.company.list', { id: companySet })).data()
        const Users = resUser
        companyFieldNames.forEach(field => {
            const row = data.value.filter(el => el.NAME === field.name)[0]
            if (Array.isArray(field.id) && field.id.length) {
                row.DISPLAYVALUE = Users.filter(el => field.id.includes(Number(el.ID)))
                    .reduce((prev, curr) => prev.TITLE + ', ' + curr.TITLE)
                row.TRUEVALUE = Users.filter(el => field.id.includes(Number(el.ID)))
            } else {
                const row = data.value.filter(el => el.NAME === field.name)[0]
                row.TRUEVALUE = Users.filter(user => user.ID === field.id)[0]
                if(row.TRUEVALUE) row.DISPLAYVALUE = Users.filter(user => user.ID === field.id)[0].TITLE

            }
            row.ALLOW = true

        })
    }
    const contactSet = Array.from(new Set(contactIDs)).flat()
    if (contactSet.length) {
        const resUser = (await callMethod('crm.contact.list', { id: contactSet })).data()
        const Users = resUser
        contactfieldNames.forEach(field => {
            const row = data.value.filter(el => el.NAME === field.name)[0]
            if (Array.isArray(field.id) && field.id.length) {
                row.DISPLAYVALUE = Users.filter(el => field.id.includes(Number(el.ID)))
                    .reduce((prev, curr) => prev.NAME + ', ' + curr.NAME)
                row.TRUEVALUE = Users.filter(el => field.id.includes(Number(el.ID)))

            } else {
                const row = data.value.filter(el => el.NAME === field.name)[0]
                row.TRUEVALUE = Users.filter(user => user.ID === field.id)[0]
                if(row.TRUEVALUE) row.DISPLAYVALUE = Users.filter(user => user.ID === field.id)[0].NAME
            }
            row.ALLOW = true

        })
    }
    const iblockelSet = Array.from(new Set(iblockelIDs)).flat()
    const iblockIDsForElSet = Array.from(new Set(iblockIDsForEl)).flat()
    if (iblockelSet.length) {
        const promisesLists = iblockIDsForElSet.map(iblock => ({
            [iblock]: {
                method: `lists.get.iblock.type.id`,
                params: { IBLOCK_ID: iblock }
            }
        }))
        const resultArr = [];
        let batchLists;
        while (promisesLists.length) {
            const subArrs = promisesLists.splice(0, 50)
            batchLists = {}
            for (let i = 0; i < subArrs.length; i++) {
                batchLists[Object.keys(subArrs[i])[0]] = Object.values(subArrs[i])[0];
            }
            const res = await callBatch(batchLists)
            resultArr.push(res)
        }
        const promisesElementsAll = []
        resultArr.forEach((elem) => {
            const iblockelfields = iblockelfieldNames.filter(field => Object.keys(elem).includes(field.IBLOCK_ID.toString())).map(field => ({
                ...field,
                IBLOCK_TYPE_ID: elem[field.IBLOCK_ID].answer.result
            }))
            promisesElementsAll.push(iblockelfields.filter(field => field.id != 0).map(field => {
                if (!Array.isArray(field.id)) {
                    return {
                        [field.id]: {
                            method: `lists.element.get`,
                            params: { IBLOCK_ID: field.IBLOCK_ID, IBLOCK_TYPE_ID: field.IBLOCK_TYPE_ID, ELEMENT_ID: field.id }
                        }
                    }
                } else {
                    return field.id.map(el => ({
                        [el]: {
                            method: `lists.element.get`,
                            params: { IBLOCK_ID: field.IBLOCK_ID, IBLOCK_TYPE_ID: field.IBLOCK_TYPE_ID, ELEMENT_ID: el }
                        }
                    }))
                }
            }))
        })
        const promisesElements = []
        promisesElementsAll.forEach(el => {
            el.forEach(elem => {
                if (Array.isArray(elem)) {
                    elem.forEach(key => {
                        promisesElements.push(key)
                    })
                } else {
                    promisesElements.push(elem)
                }
            })
        })
        const resultElems = []
        while (promisesElements.length) {
            const subArrs = promisesElements.splice(0, 50)
            batchLists = {}
            for (let i = 0; i < subArrs.length; i++) {
                batchLists[Object.keys(subArrs[i])[0]] = Object.values(subArrs[i])[0];
            }
            const res = await callBatch(batchLists)
            resultElems.push(res)
        }
        const elemMap = {}
        resultElems.forEach(elem=>{
            Object.keys(elem).forEach(el=>{
                elemMap[el] = elem[el]
            })
        })
        iblockelfieldNames.forEach(field=>{
            const row = data.value.filter(el => el.NAME === field.name)[0]
            if(Array.isArray(field.id) && field.id.length){
                row.TRUEVALUE = row.DISPLAYVALUE
                 .map(key=>({
                     ID: key,
                     NAME: elemMap[key].answer.result[0].NAME
                 })).map(field=>({ID: `${field.ID}`, NAME: `${field.NAME} [${field.ID}]`}))
                row.DISPLAYVALUE = row.DISPLAYVALUE.map(key=>({
                    ID: key,
                    NAME: elemMap[key].answer.result.length && elemMap[key].answer.result[0].NAME
                })).map(field=>`${field.NAME} [${field.ID}], `).reduce((a,b)=>a.concat(b))

                
            } else {
                const row = data.value.filter(el => el.NAME === field.name)[0]
                if(row.DISPLAYVALUE && elemMap[row.DISPLAYVALUE]){
                    const val = elemMap[row.DISPLAYVALUE].answer.result[0].ID
                    const name = elemMap[row.DISPLAYVALUE].answer.result[0].NAME
                    row.TRUEVALUE = elemMap[row.DISPLAYVALUE].answer.result[0]
                    row.DISPLAYVALUE = `${name} [${val}]`
                    row.ALLOW = true
                }
            }
            row.ALLOW = true
        })
    }
    const iblocksecIDSet = Array.from(new Set(iblocksecIDs)).flat()
    const iblockIDsForSecSet = Array.from(new Set(iblockIDsForSec)).flat()
    if (iblocksecIDSet.length) {
        const promisesLists = iblockIDsForSecSet.map(iblock => ({
            [iblock]: {
                method: `lists.get.iblock.type.id`,
                params: { IBLOCK_ID: iblock }
            }
        }))
        const resultArr = [];
        let batchLists;
        while (promisesLists.length) {
            const subArrs = promisesLists.splice(0, 50)
            batchLists = {}
            for (let i = 0; i < subArrs.length; i++) {
                batchLists[Object.keys(subArrs[i])[0]] = Object.values(subArrs[i])[0];
            }
            const res = await callBatch(batchLists)
            resultArr.push(res)
        }
        const promisesElementsAll = []
        resultArr.forEach((elem) => {
            const iblockelfields = iblocksecfieldNames.filter(field => Object.keys(elem).includes(field.IBLOCK_ID.toString())).map(field => ({
                ...field,
                IBLOCK_TYPE_ID: elem[field.IBLOCK_ID].answer.result
            }))
            promisesElementsAll.push(iblockelfields.filter(field => field.id != 0).map(field => {
                if (!Array.isArray(field.id)) {
                    return {
                        [field.id]: {
                            method: `lists.section.get`,
                            params: { IBLOCK_ID: field.IBLOCK_ID, IBLOCK_TYPE_ID: field.IBLOCK_TYPE_ID, FILTER: {
                                id: field.id
                            } }
                        }
                    }
                } else {
                    return field.id.map(el => ({
                        [el]: {
                            method: `lists.section.get`,
                            params: { IBLOCK_ID: field.IBLOCK_ID, IBLOCK_TYPE_ID: field.IBLOCK_TYPE_ID, FILTER: {
                                id: field.id
                            } }
                        }
                    }))
                }
            }))
        })
        const promisesElements = []
        promisesElementsAll.forEach(el => {
            el.forEach(elem => {
                if (Array.isArray(elem)) {
                    elem.forEach(key => {
                        promisesElements.push(key)
                    })
                } else {
                    promisesElements.push(elem)
                }
            })
        })
        const resultElems = []
        while (promisesElements.length) {
            const subArrs = promisesElements.splice(0, 50)
            batchLists = {}
            for (let i = 0; i < subArrs.length; i++) {
                batchLists[Object.keys(subArrs[i])[0]] = Object.values(subArrs[i])[0];
            }
            const res = await callBatch(batchLists)
            resultElems.push(res)
        }
        const elemMap = {}
        resultElems.forEach(elem=>{
            Object.keys(elem).forEach(el=>{
                elemMap[el] = elem[el]
            })
        })
        iblocksecfieldNames.forEach(field=>{
            const row = data.value.filter(el => el.NAME === field.name)[0]
            if(Array.isArray(field.id)){
                row.TRUEVALUE = row.DISPLAYVALUE
                row.DISPLAYVALUE = row.DISPLAYVALUE.map(key=>({
                    ID: key,
                    NAME: elemMap[key].answer.result[0].NAME
                })).map(field=>`${field.NAME} [${field.ID}], `).reduce((a,b)=>a.concat(b)) 

                
            } else {
                if(row.DISPLAYVALUE){
                    const val = elemMap[row.DISPLAYVALUE].answer.result[0].ID
                    const name = elemMap[row.DISPLAYVALUE].answer.result[0].NAME
                    row.DISPLAYVALUE = `${name} [${val}]`
                    row.TRUEVALUE = elemMap[row.DISPLAYVALUE].answer.result[0]
                }
            }
            row.ALLOW = true

        })
    }
    if(statusesfieldNames.length){
        const resStat = (await callMethod('crm.status.list',{STATUS_ID: statusesIDs})).data()
        const Statuses = resStat.map(stat => ({
            ID: stat.STATUS_ID,
            NAME: stat.NAME
        })).concat({ ID: 'undefined', NAME: ' ' })
        statusesfieldNames.forEach((field) => {
            const row = data.value.filter(el => el.NAME === field.name)[0]
            console.log(row)
            row.TRUEVALUE = Statuses.filter(stat => stat.ID === field.id)[0]
            if(row.TRUEVALUE) {row.DISPLAYVALUE = Statuses.filter(stat => stat.ID === field.id)[0].NAME}
            row.ALLOW = true

        })
    }
    const crmIDsSet = Array.from(new Set(crmIDs))
    if(crmIDsSet.length){
        console.log( Array.from(new Set(crmIDsSet.filter(el=>el.entity.includes('DEAL')))))
        let inter
        inter = Array.from(new Set(crmIDsSet.filter(el=>el.entity.includes('DEAL'))))

        const crmDealIDs = inter.map(el=>{
            if(Array.isArray(el.id)) return el.id.length ? el.id.filter(el=>el.startsWith('D_')).map(el=>String(el).substring(2)) : []
            else return el.id.startsWith('D_') && String(el.id).substring(2)
        }
        ).flat()
        inter = Array.from(new Set(crmIDsSet.filter(el=>el.entity.includes('LEAD'))))
        const crmLeadIDs =inter.map(el=>{
            if(Array.isArray(el.id)) return el.id.length ? el.id.filter(el=>el.startsWith('L_')).map(el=>String(el).substring(2)) : []
            else return el.id.startsWith('L_') && String(el.id).substring(2)
        }
        ).flat()
        inter = Array.from(new Set(crmIDsSet.filter(el=>el.entity.includes('COMPANY'))))
        const crmCompanyIDs = inter.map(el=>
            {
            if(Array.isArray(el.id)) return el.id.length ? el.id.filter(el=>el.startsWith('CO')).map(el=>String(el).substring(3)) : []
            else return el.id.startsWith('CO') && String(el.id).substring(3)
            }
        ).flat()
        inter = Array.from(new Set(crmIDsSet.filter(el=>el.entity.includes('CONTACT'))))
        const crmContactIDs = inter.map(el=>
            {
                if(Array.isArray(el.id)) return el.id.length ? el.id.filter(el=>el.startsWith('C_')).map(el=>String(el).substring(2)) : []
                else return el.id.startsWith('C_') && String(el.id).substring(2)
                }
        ).flat()
        inter = Array.from(new Set(crmIDsSet.filter(el=>el.entity.includes('SMART_INVOICE'))))
        const crmInvoiceIDs = inter.map(el=>
            {
                if(Array.isArray(el.id)) return el.id.length ? el.id.filter(el=>el.startsWith('SI_')).map(el=>String(el).substring(3)) : []
                else return el.id.startsWith('SI_') && String(el.id).substring(3)
                }
        ).flat()
        const resCrm = {}
        if(crmDealIDs.length){
            const res = await (await callMethod('crm.deal.list', { filter: { "=ID": crmDealIDs },
            select: [ "ID", "TITLE" ] })).data()
            resCrm['DEAL'] = res
            crmfieldNames.filter(el=>el.entity.includes('DEAL')).forEach(field=>{
                const row = data.value.filter(el => el.NAME === field.name)[0]
                if(Array.isArray(row.DISPLAYVALUE)){
                    const dealIDs = row.DISPLAYVALUE.filter(el=>el.startsWith('D_')).map(el=>String(el).substring(2))
                    if(res.filter(el=>dealIDs.includes(el.ID)).length){
                        row.TRUEVALUE = [...row.TRUEVALUE, ...res.filter(el=>dealIDs.includes(el.ID)).map(el=>`D_${el.ID}`)]
                        
                    }
                } else {
                    if(row.DISPLAYVALUE.startsWith('D_')){
                        const dealIDs = row.DISPLAYVALUE.substring(2)
                        if(res.filter(el=>dealIDs.includes(el.ID)).length){
                            row.TRUEVALUE = [...res.filter(el=>dealIDs.includes(el.ID)).map(el=>`D_${el.ID}`)]
                            
                        }
                    }
                }
                
                
            })
        }
        if(crmLeadIDs.length){
            console.log(crmLeadIDs)
            const res = await (await callMethod('crm.lead.list', { filter: { "=ID": crmLeadIDs },
            select: [ "ID", "NAME", "TITLE" ] })).data()
            resCrm['LEAD'] = res
            crmfieldNames.filter(el=>el.entity.includes('LEAD')).forEach(field=>{
                const row = data.value.filter(el => el.NAME === field.name)[0]
                if(Array.isArray(row.DISPLAYVALUE)){
                    const dealIDs = row.DISPLAYVALUE.filter(el=>el.startsWith('L_')).map(el=>String(el).substring(2))
                    if(res.filter(el=>dealIDs.includes(el.ID)).length){
                        console.log(res.filter(el=>dealIDs.includes(el.ID)))
                        row.TRUEVALUE = [...row.TRUEVALUE, ...res.filter(el=>dealIDs.includes(el.ID)).map(el=>`L_${el.ID}`)]
                        
                    }
                } else {
                    if(row.DISPLAYVALUE.startsWith('L_')){
                        const dealIDs = row.DISPLAYVALUE.substring(2)
                        if(res.filter(el=>dealIDs.includes(el.ID)).length){
                            row.TRUEVALUE = [...res.filter(el=>dealIDs.includes(el.ID)).map(el=>`L_${el.ID}`)]
                        }
                    }
                }
            })
        }
        if(crmCompanyIDs.length){
            const res = await (await callMethod('crm.company.list', { filter: { "=ID": crmCompanyIDs},
            select: [ "ID", "TITLE" ] })).data()
            resCrm['COMPANY'] = res
            crmfieldNames.filter(el=>el.entity.includes('COMPANY')).forEach(field=>{
                const row = data.value.filter(el => el.NAME === field.name)[0]
                if(Array.isArray(row.DISPLAYVALUE)){
                    const dealIDs = row.DISPLAYVALUE.filter(el=>el.startsWith('CO_')).map(el=>String(el).substring(3))
                    if(res.filter(el=>dealIDs.includes(el.ID)).length){
                        row.TRUEVALUE = [...row.TRUEVALUE, ...res.filter(el=>dealIDs.includes(el.ID)).map(el=>`CO_${el.ID}`)]
                        
                    }
                } else {
                    if(row.DISPLAYVALUE.startsWith('CO_')){
                        const dealIDs = row.DISPLAYVALUE.substring(3)
                        if(res.filter(el=>dealIDs.includes(el.ID)).length){
                            row.TRUEVALUE = [...res.filter(el=>dealIDs.includes(el.ID)).map(el=>`CO_${el.ID}`)]
                        }
                    }
                }
            })
        }
        if(crmContactIDs.length){
            const res = await (await callMethod('crm.contact.list', { filter: { "=ID": crmContactIDs },
            select: [ "ID", "TITLE", "NAME" ] })).data()
            resCrm['CONTACT'] = res
            crmfieldNames.filter(el=>el.entity.includes('CONTACT')).forEach(field=>{
                const row = data.value.filter(el => el.NAME === field.name)[0]
                if(Array.isArray(row.DISPLAYVALUE)){
                    const dealIDs = row.DISPLAYVALUE.filter(el=>el.startsWith('C_')).map(el=>String(el).substring(2))
                    if(res.filter(el=>dealIDs.includes(el.ID)).length){
                        row.TRUEVALUE = [...row.TRUEVALUE, ...res.filter(el=>dealIDs.includes(el.ID)).map(el=>`C_${el.ID}`)]
                        
                    }
                } else {
                    if(row.DISPLAYVALUE.startsWith('C_')){
                        const dealIDs = row.DISPLAYVALUE.substring(2)
                        if(res.filter(el=>dealIDs.includes(el.ID)).length){
                            row.TRUEVALUE = [...res.filter(el=>dealIDs.includes(el.ID)).map(el=>`C_${el.ID}`)]
                        }
                    }
                }
            })
        }
        if(crmInvoiceIDs.length){
            const res = await (await callMethod('crm.item.list', { entityTypeId: 7,filter: { "=id": crmInvoiceIDs },
            select: [ "ID", "searchContent" ] })).data().items
            resCrm['INVOICE'] = res

            crmfieldNames.filter(el=>el.entity.includes('SMART_INVOICE')).forEach(field=>{
                const row = data.value.filter(el => el.NAME === field.name)[0]
                if(Array.isArray(row.DISPLAYVALUE)){
                    const dealIDs = row.DISPLAYVALUE.filter(el=>el.startsWith('SI_')).map(el=>String(el).substring(3))
                    console.log(res, dealIDs)
                    if(res.filter(el=>dealIDs.includes(String(el.id))).length){
                        row.TRUEVALUE = [...row.TRUEVALUE, ...res.filter(el=>dealIDs.includes(String(el.id))).map(el=>`SI_${el.id}`)]
                        
                    }
                } else {
                    if(row.DISPLAYVALUE.startsWith('SI_')){
                        const dealIDs = row.DISPLAYVALUE.substring(3)
                        console.log(res.filter(el=>dealIDs.includes(el.ID)))
                        if(res.filter(el=>dealIDs.includes(el.id)).length){
                            
                            row.TRUEVALUE = [...res.filter(el=>dealIDs.includes(el.id)).map(el=>`SI_${el.id}`)]
                        }
                    }
                }
            })
        }
        crmfieldNames.forEach(field=>{
            const row = data.value.filter(el => el.NAME === field.name)[0]
            if(row.TRUEVALUE.length){
                row.DISPLAYVALUE = row.TRUEVALUE.map(el=>{
                    switch(el.substring(0,2)){
                        case 'L_': {
                            const ID = el.substring(2)
                            const NAME = resCrm['LEAD'].filter(elem=>elem.ID==ID)[0].TITLE
                            return `: ${NAME} [${ID}], `
                        }
                        case 'D_': {
                            const ID = el.substring(2)
                            const NAME = resCrm['DEAL'].filter(elem=>elem.ID==ID)[0].TITLE
                            return `: ${NAME} [${ID}], `
                        }
                        case 'C_': {
                            const ID = el.substring(2)
                            const NAME = resCrm['CONTACT'].filter(elem=>elem.ID==ID)[0].NAME
                            return `: ${NAME} [${ID}], `
                        }
                        case 'SI': {
                            const ID = el.substring(3)
                            console.log(resCrm['INVOICE'])
                            const NAME = resCrm['INVOICE'].filter(elem=>elem.id==ID)[0].searchContent
                            return `: ${NAME} [${ID}], `
                        }
                        case 'CO': {
                            const ID = el.substring(3)
                            const NAME = resCrm['COMPANY'].filter(elem=>elem.ID==ID)[0].TITLE
                            return `: ${NAME} [${ID}], `
                        }
                    }
                }).join('')
            }
            row.ALLOW = true

        })
    }
    
}
