import { placement, callMethod } from 'bx24-api';

export async function* getFields(){
    const table = []
    const fetchObj = {}
    const res = await placement.info()
    let place = res.placement.split('_')[1].toLowerCase()
    let entityID = res.options.ID
    const entityTypeID = res.placement.split('_')[2]
    const contactTypes = {
        WORK: '',
        MOBILE: '',
        FAX: '',
        HOME: '',
        PAGER: '',
        MAILING: ' ',
        OTHER: '',
        OPENLINE: '-',
        IMOL: ' ',
    }
    class Row {
        constructor(NAME, DESCRIPTION, TYPE,
            DISPLAYVALUE, PERMISSIONS, EDITOR, IBLOCKID, CRMENTITY, TRUEVALUE, ENUM){
                this.NAME = NAME
                this.DESCRIPTION = DESCRIPTION
                this.TYPE = TYPE
                this.DISPLAYVALUE = DISPLAYVALUE 
                this.PERMISSIONS = PERMISSIONS
                this.EDITOR = EDITOR
                this.IBLOCKID = IBLOCKID
                this.CRMENTITY = CRMENTITY
                this.TRUEVALUE = TRUEVALUE
                this.ENUM = ENUM

        }
    }
    let entityFields
    let fieldsArray
    if(place==='dynamic'){
        const entityTypeId = res.placement.split('_')[2]
        const entityID = res.options.ID
        entityFields = await (await callMethod(`crm.item.get`, { id: entityID, entityTypeId })).data()
        fieldsArray = await (await callMethod(`crm.item.fields`, {entityTypeId})).data().fields
        console.log(fieldsArray)
    } else if(place==='smart'){
        const entityTypeId = 31
        const entityID = res.options.ID
        entityFields = await (await callMethod(`crm.item.get`, { id: entityID, entityTypeId })).data()
        fieldsArray = await (await callMethod(`crm.item.fields`, {entityTypeId})).data().fields
        console.log(fieldsArray)
    } 
    else {
        entityFields = await (await callMethod(`crm.${place}.get`, { id: entityID })).data()
        fieldsArray = await (await callMethod(`crm.${place}.fields`, {})).data()
    }
    Object.keys(fieldsArray).forEach((key)=>{
        let permissions = []
        let editor = ''
        let enumItems = []
        let iblockid = 0
        let description = ''
        let entity = ''
        fieldsArray[key].formLabel
                        ? description = fieldsArray[key].formLabel
                        : description = fieldsArray[key].title
        if(fieldsArray[key].isDynamic) permissions.push('')
        if(fieldsArray[key].isImmutable) permissions.push('')
        if(fieldsArray[key].isMultiple) permissions.push('')
        if(fieldsArray[key].isReadOnly) permissions.push('  ')
        if(fieldsArray[key].isRequired) permissions.push('')

        if (fieldsArray[key].isMultiple && fieldsArray[key].type==='crm_multifield') editor = 'multiedit'
        if ((fieldsArray[key].isMultiple && fieldsArray[key].type!=='crm_multifield') || (fieldsArray[key].items &&fieldsArray[key].isMultiple )){
            if(fieldsArray[key].type==='string'||fieldsArray[key].type==='integer'||fieldsArray[key].type==='double'||fieldsArray[key].type==='url'){
                editor = 'textArray'
            } else {
                editor = 'checkBoxes'
            }
        }
        else if(fieldsArray[key].items && !fieldsArray[key].isMultiple){
            editor = 'selectBox'
        }
        if (fieldsArray[key].type === 'enumeration' && editor ==='checkBoxes' ) {
            console.log(fieldsArray[key])
            fieldsArray[key].items.forEach(item => {
                enumItems.push(item)
            });

        } else if (fieldsArray[key].type === 'enumeration' && editor ==='selectBox'){
            fieldsArray[key].items.forEach(item => {
                enumItems.push(item)
            });
        }
        if (fieldsArray[key].type.startsWith('iblock_')) {
            iblockid = fieldsArray[key].settings.IBLOCK_ID
        };
        if(fieldsArray[key].type ==='crm'){
            entity = Object.keys(fieldsArray[key].settings).filter(el=>
                fieldsArray[key].settings[el] === 'Y'
            )

        }
        if((fieldsArray[key].type==='string'||fieldsArray[key].type==='integer'||fieldsArray[key].type==='double'||fieldsArray[key].type==='url') && !fieldsArray[key].isMultiple ){
            editor = 'textBox'
        }
        if(fieldsArray[key].type==='file') console.log(fieldsArray[key])
        if((fieldsArray[key].type === 'user'
        ||fieldsArray[key].type === 'employee'
        ||fieldsArray[key].type === 'crm_company'
        ||fieldsArray[key].type === 'iblock_element'
        ||fieldsArray[key].type === 'iblock_section'
        ||fieldsArray[key].type === 'crm_status'
        ||fieldsArray[key].type === 'crm_currency'
        ||fieldsArray[key].type === 'crm_contact'
        ||fieldsArray[key].type==='char'
        ) && !fieldsArray[key].isMultiple){
            editor = 'selectBox'
        }
        if(fieldsArray[key].type === 'crm_quote'
        ||fieldsArray[key].type === 'money'
        ||fieldsArray[key].type === 'resourcebooking'
        ||fieldsArray[key].type === 'file'
        ||fieldsArray[key].type === 'location'
        ||fieldsArray[key].type === 'crm_category'
        ){
            editor = 'nan'
        }
        table.push(new Row(key, description, fieldsArray[key].type, '', permissions, editor, iblockid, entity, '', enumItems))

    })
    table.forEach(row=>{
        const rowName = row.NAME
        const rowValue = entityFields[row.NAME]
        if(row.TYPE==='crm_multifield'){
            row.ALLOW = true
            row.TRUEVALUE = rowValue
            rowValue && rowValue.length && rowValue.forEach(item=>{
                if(contactTypes[item.VALUE_TYPE]){
                    row.DISPLAYVALUE += `${contactTypes[item.VALUE_TYPE]}: ${item.VALUE},`;
                } else {
                    row.DISPLAYVALUE += `${item.VALUE_TYPE}: ${item.VALUE}, `;
                }
            })
        }
        if(row.EDITOR==='checkBoxes'){
            row.ALLOW = true
            console.log(rowValue)
            if(rowValue) row.TRUEVALUE = row.ENUM.filter(el=>rowValue.includes(Number(el.ID)))
            if(row.TRUEVALUE) row.DISPLAYVALUE = row.TRUEVALUE.map(el=>el.VALUE).join(', ')

        }
        if(row.EDITOR==='textBox'){
            row.ALLOW = true

            row.TRUEVALUE = rowValue
            row.DISPLAYVALUE = rowValue
        }
        if(row.EDITOR==='textArray'){
            row.ALLOW = true
            console.log(rowValue)
            row.TRUEVALUE = rowValue
            if(rowValue) row.DISPLAYVALUE = rowValue.join(', ')
        }
        if(row.EDITOR==='selectBox' && row.TYPE==='enumeration'){
            row.ALLOW = true
                if(!rowValue) return
                row.DISPLAYVALUE = row.ENUM.filter(el=>el.ID==rowValue)[0].VALUE
                row.TRUEVALUE = row.ENUM.filter(el=>el.ID==rowValue)[0]
        }
        if(row.TYPE==='char'){
            row.ALLOW = true

            row.DISPLAYVALUE = rowValue
            row.TRUEVALUE ={ID:rowValue, NAME:rowValue}
        }
        if(row.TYPE==='boolean'){
            row.ALLOW = true

            row.EDITOR = 'selectBox'
            if(rowValue==1){
                row.TRUEVALUE = {
                    ID:1,
                    NAME:''
                }
                row.DISPLAYVALUE = ''
            } else if(rowValue==0){
                row.TRUEVALUE = {
                    ID:0,
                    NAME: ''
                }
                row.DISPLAYVALUE = ''
            }
            //row.DISPLAYVALUE = row.SELECTARRAY.filter(el=>el.ID==rowValue).VALUE
        }
       
        if(row.TYPE==='crm'){
            row.DISPLAYVALUE = rowValue
            if(row.PERMISSIONS.includes('')){
                row.EDITOR = 'crmMulti'
            } else {
                row.EDITOR = 'crm'
            }
            
            fetchObj[rowName] = {
                type: row.TYPE,
                entity: row.CRMENTITY,
                value: rowValue
            }
        }
        if(row.TYPE==='user' || row.TYPE==='employee' || row.TYPE==='crm_company' || row.TYPE==='crm_status' || row.TYPE==='crm_currency'|| row.TYPE==='crm_contact'){
            row.DISPLAYVALUE = rowValue
            fetchObj[rowName] = {
                type: row.TYPE,
                value: rowValue
            }
        }
        if(row.TYPE==='crm_currency') row.ALLOW = true
        if(row.TYPE==='iblock_element' || row.TYPE==='iblock_section'){
            row.DISPLAYVALUE = rowValue
            fetchObj[rowName] = {
                type: row.TYPE,
                value: rowValue,
                iblockid: row.IBLOCKID
                
            }
        }
        if(row.TYPE==='datetime'){
            row.ALLOW = true
            row.EDITOR = 'datetime'
            if(!rowValue) return
            row.DISPLAYVALUE = new Date(rowValue).toLocaleString('RU')
            row.TRUEVALUE = rowValue
        }
        if(row.TYPE==='date'){
            row.ALLOW = true
            row.EDITOR = 'date'
            if(!rowValue) return
            row.DISPLAYVALUE = new Date(rowValue).toLocaleDateString('RU')
            row.TRUEVALUE = rowValue
        }

        
        
    })
    return [table, fetchObj]
}