<script setup>
import {
    DxColumnChooser,
    DxDataGrid,
    DxEditing,
    DxExport,
    DxFilterRow,
    DxForm,
    DxGroupPanel,
    DxHeaderFilter,
    DxSearchPanel,
} from 'devextreme-vue/data-grid';
import { DxItem } from 'devextreme-vue/form';
import { onMounted, ref } from 'vue';

import { getFields } from '../api/getFields'
import { getAdditional } from '../api/getAdditional'
import columns from './fields'

import EnumVue from './editorTemplates/Enum.vue'
import SelectVue from './editorTemplates/Select.vue'
import MultifieldVue from './editorTemplates/Multifield.vue'
import CrmVue from './editorTemplates/Crm.vue';
import CrmMulti from './editorTemplates/CrmMulti.vue'

import _ from 'lodash';
import { callBatch, callMethod, placement } from 'bx24-api';

import { notification } from 'ant-design-vue';
import { SaveOutlined } from '@ant-design/icons-vue';
import DatePicker from './editorTemplates/DatePicker.vue';
import TextBoxArr from './editorTemplates/TextBoxArr.vue';
import TextBox from './editorTemplates/TextBox.vue';


const data = ref([])
const fetch = ref({})
const cols = ref([])
const dataGridRefKey = ref(null)
const countLoad = ref(false)
const spinning = ref(true)
const editorTemplate = ref('')
const btn_mess = ref('')
const pendingValue = ref([])
const pendingDisplay = ref('')
const label = ref('')
const editingRow = ref({})
const enumMultiple = ref(false)
const isEditing = ref(false)

onMounted(async () => {
    const res = await getFields().next()
    data.value = res.value[0]
    fetch.value = res.value[1]
    spinning.value = false
    cols.value = columns
    await getAdditional(data, fetch).next()

})

const onRowPrep = (e) => {
    if (!e.row) return
    dataGridRefKey.value.instance.getVisibleRows().forEach(row=>{
        if(!row.cells[6]) return
        row.cells[6].cellElement.style.visibility = 'hidden'
    })
    switch (e.row.data.EDITOR) {
        case 'textBox': {
            editorTemplate.value = 'textBox'
            break
        }
        case 'textArray':{
            editorTemplate.value = 'textArr'
            break
        }
        case 'crm': {
            editorTemplate.value = 'crm'
            break
        }
        case 'date':
        case 'datetime':{
            editorTemplate.value = 'datetime'
            break;
        }
        case 'checkBoxes': {
            editorTemplate.value = 'enum'
            enumMultiple.value = true
            break;
        }
        case 'selectBox': {
            editorTemplate.value = 'select'
            break;
        }
        case 'multiedit': {
            editorTemplate.value = 'multifield'
            break
        }
        case 'crmMulti':{
            editorTemplate.value = 'crmMulti'
            break;
        }
        default:
            editorTemplate.value = 'notSupported'
    }
    isEditing.value = true
    label.value = e.row.data.NAME
    editingRow.value = e.row.data
}
const onRowSaving = (e) => {
    e.changes = [{
        data: {
            TRUEVALUE: pendingValue.value,
            DISPLAYVALUE: pendingDisplay.value
        },
        key: editingRow.value.NAME,
        type: 'update'
    }]
    editorTemplate.value = ''
    isEditing.value = false
    dataGridRefKey.value.instance.getVisibleRows().forEach(row=>{
        if(!row.cells[6]) return
        row.cells[6].cellElement.style.visibility = 'visible'
    })
}
const onRowCancel = () => {
    editorTemplate.value = ''
    pendingValue.value = []
    console.log(pendingValue.value, 'cancel')
    isEditing.value = false
    dataGridRefKey.value.instance.getVisibleRows().forEach(row=>{
        if(!row.cells[6]) return
        row.cells[6].cellElement.style.visibility = 'visible'
    })

}
const changePending = (pending, display) => {
    pendingValue.value = _.cloneDeep(pending)
    pendingDisplay.value = display
    console.log(pendingValue.value, pendingDisplay.value, 'pend')
}
const countField = async () => {
    notification.open({
        message: '    '
    })
    countLoad.value = true

    const fieldNames = [];
    let batchLists = {};
    const resultArr = [];
    data.value.forEach(row => {
        const field = row.NAME
        fieldNames.push(field)
    })
    
    const place = await placement.info()
    let ent = place.placement.split('_')[1].toLowerCase()
    let entityTypeId
    let promisesLists
    if(ent==='smart'){
        ent = 'item'
        entityTypeId = 31
        promisesLists = fieldNames.map(field => ({
            [field]: {
                method: `crm.item.list`,
                params: { filter: { [`!${field}`]: "" }, entityTypeId }
            }
        }))
    } else if(ent==='dynamic'){
        ent = 'item'
        entityTypeId = place.placement.split('_')[2]
        promisesLists = fieldNames.map(field => ({
            [field]: {
                method: `crm.item.list`,
                params: { filter: { [`!${field}`]: "" }, entityTypeId }
            }
        }))
    } else {
        promisesLists = fieldNames.map(field => ({
            [field]: {
                method: `crm.${ent}.list`,
                params: { filter: { [`!${field}`]: "" } }
            }
        }))
    }
    try{

        while (promisesLists.length) {
            const subArrs = promisesLists.splice(0, 50)
            batchLists = {}
            for (let i = 0; i < subArrs.length; i++) {
                batchLists[Object.keys(subArrs[i])[0]] = Object.values(subArrs[i])[0];
            }
            console.log(batchLists)
            const res = await callBatch(batchLists)
            resultArr.push(res)
        }
        const resFlat = resultArr.flat()
        console.log(resFlat)
        resFlat.forEach(field => {
            Object.keys(field).forEach(key => {
                data.value.find(row => row.NAME === key).COUNTUSES = field[key].answer.total
                console.log(field[key].answer.total)
            })
        })
        countLoad.value = false
    } catch(e){
        notification.open({
            message: '   ',

        })
        countLoad.value = false

    }
}
const onDBSave = async () => {
    btn_mess.value = '...'
    let res = await placement.info()
    let place = res.placement.split('_')[1].toLowerCase();
    let id = res.options.ID;
    let queries = {}
    data.value.forEach(row => {
        if (!row.TRUEVALUE) {
            queries[row.NAME] = row.DISPLAYVALUE
        }
        else {
            if (!Array.isArray(row.TRUEVALUE)) {
                if(row.EDITOR!=='date' && row.EDITOR!=='datetime' && row.TYPE!=='string'  && row.TYPE!=='integer' && row.TYPE!=='double'){
                queries[row.NAME] = row.TRUEVALUE.ID
                } else {
                    queries[row.NAME] = row.TRUEVALUE
                }
                
            }
            else {
                queries[row.NAME] = row.TRUEVALUE.map(el=>el.ID)
                if(row.TYPE==='crm_multifield'||(row.TYPE==='crm'&&row.EDITOR === 'crmMulti')||row.EDITOR==='textArray'){
                    queries[row.NAME] = row.TRUEVALUE
                    console.log(row.TRUEVALUE)
                }
                if(row.TYPE==='crm'&&row.EDITOR === 'crm'){
                    queries[row.NAME] = row.TRUEVALUE[0]
                }
            }

        }
    })
    await callMethod(
        `crm.${place}.update`,
        {
            id: id,
            fields: queries,
            params: { "REGISTER_SONET_EVENT": "Y" }
        }).then((result) => {
            if (result.error()) {
                console.error(result.error());
                btn_mess.value = '   ';
                setTimeout(() => {
                    btn_mess.value = '';
                }, 2000);
            } else {
                btn_mess.value = ' ';
                setTimeout(() => {
                    btn_mess.value = '';
                }, 2000);
            }
        }
        );
}

const log = (e) =>{
    if(e.column?.command==='edit' && e.row){
        if(e.row.data.PERMISSIONS.includes('  ')){
            e.cellElement.style.opacity = 0
            e.cellElement.style.visibility = 'hidden'
            return
        }
        if(e.row.data.ALLOW) {
            e.cellElement.style.opacity = 100
            e.cellElement.style.visibility = 'visible'
            return
        }
        if(e.row.data.EDITOR ==='nan'){
            e.cellElement.style.visibility = 'hidden'
            e.cellElement.style.opacity = 0
            return
        }
        if(!e.row.data.DISPLAYVALUE){
            e.cellElement.style.visibility = 'visible'
            e.cellElement.style.opacity = 100
            return
        }
        

    }

}
</script>
<template>
    <div id="app-container">
        <p>         ,   </p>
        <a-spin wrapperClassName="spinner"
            :spinning="spinning" style="display:flex; justify-content:center; align-content:center;">
            <br/>
            <a-button v-if="!spinning" :loading="countLoad" @click="countField">
                   
            </a-button>
            <a-button type='primary' id='btn_save2' @click='onDBSave' style="display:block; position:absolute; top:0%">
                <SaveOutlined style="pointer-events:none" /> {{ btn_mess }}
            </a-button>
            <DxDataGrid
                @cell-prepared="log"
                focused-row-enabled id="dataGrid" :data-source='data' key-expr='NAME' :allow-editing="true"
                :allow-column-reordering="true" ref="dataGridRefKey" :columns="cols"
                @editor-preparing="onRowPrep($event)" @saving="onRowSaving" @edit-canceled="onRowCancel($event)">
                <DxHeaderFilter :visible="true" />
                <DxColumnChooser :enabled="true" />
                <DxExport :enabled="true" />
                <DxGroupPanel :visible='true' />
                <DxSearchPanel :visible='true' />
                <DxFilterRow :visible='true' />
                <template #grid-cell="{ data }">
                    <div onmouseover="this.style.wordWrap = 'break-word' ;this.style.overflow = 'wrap';this.style.whiteSpace = 'normal'"
                        onmouseout="this.style.wordWrap = '';this.style.overflow = '';this.style.whiteSpace = '';">
                        {{ data.value?.NAME || data.value }}
                    </div>
                </template>
                <DxEditing mode="form" :use-icons="true" :height="500" :allow-updating="true">

                    <DxForm :col-span="2" :editor-options="{ height: 300 }" scrolling-enabled
                        style="display:flex; flex-direction:column">
                        <DxItem :data-field="'DISPLAYVALUE'" :template="editorTemplate || null" />
                        <DxItem template="label" />
                    </DxForm>
                </DxEditing>
                <template #label="{}">
                    <label style="display:block">{{ label }}</label>
                </template>
                <template #textArr="{}">
                    <TextBoxArr @change-pending="changePending" :row="editingRow"/>
                </template>
                <template #textBox="{}">
                    <TextBox @change-pending="changePending" :row="editingRow"/>
                </template>
                <template #crm="{}">
                    <CrmVue @change-pending="changePending" :row="editingRow" />
                </template>
                <template #enum="{}">
                    <EnumVue @change-pending="changePending" :row="editingRow" />
                </template>
                <template #select="{}">
                    <SelectVue @change-pending="changePending" :row="editingRow" />
                </template>
                <template #multifield="{}">
                    <MultifieldVue @change-pending="changePending" :row="editingRow" />
                </template>
                <template #datetime="{}">
                    <DatePicker @change-pending="changePending" :row="editingRow"/>
                </template>
                <template #crmMulti="{}">
                    <CrmMulti @change-pending="changePending" :row="editingRow" />
                </template>
                <template #notSupported="{}">
                    <h2>   </h2>
                </template>
            </DxDataGrid>
            <a-button v-if="!spinning" type='primary' id='btn_save1' @click='onDBSave' style="display:block; position:absolute; bottom:0%">
                <SaveOutlined style="pointer-events:none" /> {{ btn_mess }}
            </a-button>
        </a-spin>
    </div>

</template>
<style scoped>

:root {
  font-family: Inter, Avenir, Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;

  color-scheme: light dark;
  color: rgba(255, 255, 255, 0.87);
  background-color: #242424;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}



.card {
  padding: 2em;
}

#app-container {
  margin: 0 auto;
  padding: 2rem;
  text-align: center;
}



</style>