<script setup>

import { callMethod } from 'bx24-api';
import DxDropDownBox from 'devextreme-vue/drop-down-box';
import DxTreeView from 'devextreme-vue/tree-view';
import { onMounted, ref } from 'vue';
import _ from 'lodash'
const props = defineProps(['row', 'valueRef'])
const row = ref({})
const selectArray = ref([])
const value = ref([])
const drop = ref(null)
const cachedBox = ref([])
const emit = defineEmits(['changePending']);
const lessThanFifty = ref(false)
onMounted(async () => {
    row.value = props.row

    switch (row.value.TYPE) {
        case 'enumeration': {
            selectArray.value = row.value.ENUM.map(el=>({
                ID:el.ID,
                NAME: el.VALUE
            })).map(el => {
                if (row.value.TRUEVALUE && row.value.TRUEVALUE.length && row.value.TRUEVALUE.filter(val => val.ID == el.ID).length) {
                    return {
                        ...el,
                        CHECK: true
                    }
                } else return {
                    ...el
                }
            })
            if(Array.isArray(row.value.TRUEVALUE)) value.value = _.cloneDeep(row.value.TRUEVALUE)
            else value.value = []
            cachedBox.value = _.cloneDeep(selectArray.value)
            break
        }
        case 'iblock_section':{
            const iblockid = row.value.IBLOCKID
            const type = await (await callMethod(`lists.get.iblock.type.id`, { IBLOCK_ID: iblockid })).data()
            const params = { "IBLOCK_ID": iblockid, IBLOCK_TYPE_ID: type }
            selectArray.value = await (await callMethod('lists.section.get', params)).data().map(el => ({
                ID: el.ID,
                NAME: `${el.NAME} [${el.ID}]`
            })).map(el => {
                if (row.value.TRUEVALUE && row.value.TRUEVALUE.length && row.value.TRUEVALUE.filter(val => val.ID == el.ID).length) {
                    return {
                        ...el,
                        CHECK: true
                    }
                } else return {
                    ...el
                }
            })
            if(Array.isArray(row.value.TRUEVALUE)) value.value = _.cloneDeep(row.value.TRUEVALUE)
            else value.value = []
            cachedBox.value = _.cloneDeep(selectArray.value)
            break
        }
        case 'crm_company':{
            selectArray.value = await (await callMethod(`crm.company.list`)).data()
                .map(user => ({
                    ID: user.ID,
                    NAME: user.TITLE
                }))
                .map(el => {
                    if (row.value.TRUEVALUE && row.value.TRUEVALUE.length && row.value.TRUEVALUE.filter(val => val.ID == el.ID).length) {
                        return {
                            ...el,
                            CHECK: true
                        }
                    } else return {
                        ...el
                    }
                })
                if(Array.isArray(row.value.TRUEVALUE)) value.value = _.cloneDeep(row.value.TRUEVALUE)
            else value.value = []
            break
        }
        case 'crm_contact': {
            selectArray.value = await (await callMethod(`crm.contact.list`)).data()
                .map(user => ({
                    ID: user.ID,
                    NAME: user.NAME
                }))
                .map(el => {
                    if (row.value.TRUEVALUE && row.value.TRUEVALUE.length && row.value.TRUEVALUE.filter(val => val.ID == el.ID).length) {
                        return {
                            ...el,
                            CHECK: true
                        }
                    } else return {
                        ...el
                    }
                })
            if(Array.isArray(row.value.TRUEVALUE)) value.value = _.cloneDeep(row.value.TRUEVALUE)
            else value.value = []
            break
        }
        case 'iblock_element': {
            const iblockid = row.value.IBLOCKID
            const type = await (await callMethod(`lists.get.iblock.type.id`, { IBLOCK_ID: iblockid })).data()
            const params = { "IBLOCK_ID": iblockid, IBLOCK_TYPE_ID: type }
            selectArray.value = await (await callMethod('lists.element.get', params)).data().map(el => ({
                ID: el.ID,
                NAME: `${el.NAME} [${el.ID}]`
            }))
                .map(el => {
                    if (row.value.TRUEVALUE.filter(val => val.ID == el.ID).length) {
                        return {
                            ...el,
                            CHECK: true
                        }
                    } else return {
                        ...el
                    }
                })
                if(Array.isArray(row.value.TRUEVALUE)) value.value = _.cloneDeep(row.value.TRUEVALUE)
            else value.value = []
            console.log(selectArray.value)
            cachedBox.value = _.cloneDeep(selectArray.value)
            if (cachedBox.value.length < 50) lessThanFifty.value = true
            break
        }
        case 'user':
        case 'employee': {
            const params = {
                FILTER: {
                    USER_TYPE: row.value.TYPE
                }
            }
            selectArray.value = await (await callMethod('user.get', params)).data()
                .map(user => ({
                    ID: user.ID,
                    NAME: user.NAME !== '' ? `${user.NAME} ${user.LAST_NAME}` : user.EMAIL
                }))
                .map(el => {
                    if (row.value.TRUEVALUE.filter(val => val.ID == el.ID).length) {
                        return {
                            ...el,
                            CHECK: true
                        }
                    } else return {
                        ...el
                    }
                })
            console.log(selectArray.value)
            cachedBox.value = _.cloneDeep(selectArray.value)
            if(Array.isArray(row.value.TRUEVALUE)) value.value = _.cloneDeep(row.value.TRUEVALUE)
            else value.value = []
            if (cachedBox.value.length < 50) lessThanFifty.value = true

        }
    }
})
const treeChange = (e) => {
    console.log(e)
    const item = e.itemData
    console.log('imchangiiiin', item)
    if (e.itemData.CHECK) {
        console.log(value)
        value.value.push(item)
    } else {
        const elemToDel = value.value.find(el => el.ID == item.ID)
        const indexToDel = value.value.indexOf(elemToDel)
        value.value.splice(indexToDel, 1)
    }
    console.log(value.value)
    let display
    if (value.value.length) {
        display = selectArray.value.filter(el => value.value.filter(elem =>
            elem.ID == el.ID).length)
            .map(elem => `${elem.NAME}, `)
            .reduce((a, b) => a.concat(b))
        console.log(display, 'gay')
    } else display = ''
    let pend = _.cloneDeep(value.value)
    
    emit('changePending', _.cloneDeep(pend), display)

}
const handleOpen = () => {
    console.log('hey sexy')
    document.getElementsByClassName('dx-texteditor-input')[1].addEventListener('input', handleInput)
    document.getElementsByClassName('dx-icon-clear')[0]
        .addEventListener('click', () => {
            selectArray.value = _.cloneDeep(cachedBox.value)
        })
}
const handleInput = async (e) => {
    if (lessThanFifty) return
    switch (row.value.TYPE) {
        case 'crm_company':{
            selectArray.value = await (await callMethod(`crm.company.list`, {FILTER:{'%TITLE': e.target.value}})).data()
                .map(user => ({
                    ID: user.ID,
                    NAME: user.TITLE
                }))
                .map(el => {
                    if (row.value.TRUEVALUE && row.value.TRUEVALUE.length && row.value.TRUEVALUE.filter(val => val.ID == el.ID).length) {
                        return {
                            ...el,
                            CHECK: true
                        }
                    } else return {
                        ...el
                    }
                })
            break
        }
        case 'iblock_section': {
            const iblockid = row.value.IBLOCKID
            const type = await (await callMethod(`lists.get.iblock.type.id`, { IBLOCK_ID: iblockid })).data()
            const params = { "IBLOCK_ID": iblockid, IBLOCK_TYPE_ID: type, FILTER:{
                '%NAME': e.target.value
            } }
            selectArray.value = await (await callMethod('lists.section.get', params)).data().map(el => ({
                ID: el.ID,
                NAME: `${el.NAME} [${el.ID}]`
            })).map(el => {
                if (row.value.TRUEVALUE && row.value.TRUEVALUE.length && row.value.TRUEVALUE.filter(val => val == el.ID).length) {
                    return {
                        ...el,
                        CHECK: true
                    }
                } else return {
                    ...el
                }
            }).forEach(elem => {
                    console.log(selectArray.value.includes(el => el.ID == elem.ID))
                    if (selectArray.value.filter(el => el.ID == elem.ID).length) return
                    else selectArray.value.push(elem)
                })
            if(row.value.TRUEVALUE){
                value.value = _.cloneDeep(row.value.TRUEVALUE.ID)
            }
            cachedBox.value = _.cloneDeep(selectArray.value)
            break
        }
        case 'crm_contact': {
            await (await callMethod('crm.contact.list',{
                FILTER:{
                    '%NAME': e.target.value
                }
            })).data().map(el => ({
                ID: el.ID,
                NAME: `${el.NAME} [${el.ID}]`
            }))
                .map(el => {
                    if (row.value.TRUEVALUE && row.value.TRUEVALUE.length && row.value.TRUEVALUE.filter(val => val == el.ID).length) {
                        return {
                            ...el,
                            CHECK: true
                        }
                    } else return {
                        ...el
                    }
                })
                .forEach(elem => {
                    console.log(selectArray.value.includes(el => el.ID == elem.ID))
                    if (selectArray.value.filter(el => el.ID == elem.ID).length) return
                    else selectArray.value.push(elem)
                })
            break
        }
        case 'iblock_element': {
            const iblockid = row.value.IBLOCKID
            const type = await (await callMethod(`lists.get.iblock.type.id`, { IBLOCK_ID: iblockid })).data()
            const params = { "IBLOCK_ID": iblockid, IBLOCK_TYPE_ID: type, FILTER: { "SEARCHABLE_CONTENT": `%${e.target.value}%` } }
            await (await callMethod('lists.element.get', params)).data().map(el => ({
                ID: el.ID,
                NAME: `${el.NAME} [${el.ID}]`
            }))
                .map(el => {
                    if (row.value.TRUEVALUE.filter(val => val == el.ID).length) {
                        return {
                            ...el,
                            CHECK: true
                        }
                    } else return {
                        ...el
                    }
                })
                .forEach(elem => {
                    console.log(selectArray.value.includes(el => el.ID == elem.ID))
                    if (selectArray.value.filter(el => el.ID == elem.ID).length) return
                    else selectArray.value.push(elem)
                })
            break
        }
        case 'user':
        case 'employee': {
            await (await callMethod('user.search', { 'FIND': `${e.target.value}` })).data().map(el => ({
                ID: user.ID,
                NAME: user.NAME !== '' ? `${user.NAME} ${user.LAST_NAME}` : user.EMAIL
            }))
                .map(el => {
                    if (row.value.TRUEVALUE.filter(val => val == el.ID).length) {
                        return {
                            ...el,
                            CHECK: true
                        }
                    } else return {
                        ...el
                    }
                })
                .forEach(elem => {
                    console.log(selectArray.value.includes(el => el.ID == elem.ID))
                    if (selectArray.value.filter(el => el.ID == elem.ID).length) return
                    else selectArray.value.push(elem)
                })
            break;
        }
    }
}
const clear = () =>{
    emit('changePending', '', '')
}
</script>
<template>
    <a-button @click="clear">  </a-button>
    <br/>
    <DxDropDownBox :value="value" :data-source="selectArray" value-expr="ID" display-expr="NAME" @opened="handleOpen">
        <DxTreeView @opened="handleOpen" selected-expr="CHECK" ref="drop" :value="value" data-structure="plain"
            selection-mode="multiple" show-check-boxes-mode="normal" @item-selection-changed="treeChange" :opened="true"
            :search-enabled="true" key-expr="ID" display-expr="NAME" :data-source="selectArray" />
    </DxDropDownBox>
</template>