<script setup>
import { DxDateBox } from 'devextreme-vue'
import { onBeforeMount, ref } from 'vue'
const props = defineProps(['row'])
const value = ref(new Date())
const type = ref('')
const emit = defineEmits(['changePending'])
onBeforeMount(()=>{
    type.value = _.cloneDeep(props.row.EDITOR)
    value.value = _.cloneDeep(props.row.TRUEVALUE)
    console.log(type.value)
})
const handleChange=(e)=>{
    const val = e.value
    let display
    if(type.value==='datetime'){
        display = new Date(val).toLocaleString('RU')
    } else {
        display = new Date(val).toLocaleDateString('RU')
    }
    emit('changePending', val, display)
}
const clear = () =>{
    emit('changePending', '', '')
}
</script>
<template>

    <a-button @click="clear">  </a-button>
    <br/>
    <DxDateBox @value-changed="handleChange" :type="type" :value="value"/>
</template>