<script setup >
import { notification } from 'ant-design-vue';
import { onMounted, ref } from 'vue';
import ActionButton from '../components/ActionButton.vue';
import { callMethod } from 'bx24-api'

const btnArray = ref(
    [{
        isOn: true,
        targetType: "leads"
    }, {
        isOn: true,
        targetType: "deals"
    }, {
        isOn: true,
        targetType: "contacts"
    }, {
        isOn: true,
        targetType: "companies"
    },
    {
        isOn:true,
        targetType: "smart"
    }, 
    {
        isOn: true,
        targetType: "quotes"
    }, {
        isOn: true,
        targetType: "invoices"
    }
]
    )
    onMounted(async ()=>{
        let domain = location.origin + location.pathname;
        let res= await callMethod('placement.get', {})
        res.data().forEach((placement=>{
           if (placement["handler"]!==domain) return
           switch (placement["placement"]){
            case "CRM_LEAD_DETAIL_TAB" : btnArray.value[0].isOn = false; break;
            case "CRM_DEAL_DETAIL_TAB" : btnArray.value[1].isOn = false; break;
            case "CRM_CONTACT_DETAIL_TAB" : btnArray.value[2].isOn = false; break;
            case "CRM_COMPANY_DETAIL_TAB" : btnArray.value[3].isOn = false; break;
            case "CRM_QUOTE_DETAIL_TAB" : btnArray.value[5].isOn = false; break;
            case "CRM_SMART_INVOICE_DETAIL_TAB": btnArray.value[6].isOn = false; break;
           }
           if(String(placement['placement']).startsWith('CRM_DYNAMIC')){
            btnArray.value[4].isOn = false;
           }
        }))
    })
    const notifyMessage = (on, type) =>{
        switch (type) {
                case 'leads' : return on ? '' : ''
                case 'deals' : return on ? '' : ''
                case 'companies' : return on ? '' : ''
                case 'contacts' : return on ? '' : ''
                case 'quotes' : return on ? ' ' : ' '
                case 'invoices' : return on ? ' ' : ' '
                case 'smart' : return on ? ' ' : ' '
            }
    }
    const clickHandler = async(event) =>{
            const targetbtn = btnArray.value.find((btn)=>btn.targetType===event.currentTarget.attributes.tarType.value)
            const index = btnArray.value.indexOf(targetbtn)
            console.log(index)
            let domain = location.origin + location.pathname;
            console.log(domain)
            let placement
            if(targetbtn){
                switch (targetbtn.targetType) {
                case 'leads' : placement =  'CRM_LEAD_DETAIL_TAB'; break;
                case 'deals' : placement =  'CRM_DEAL_DETAIL_TAB'; break;
                case 'companies' : placement =  'CRM_COMPANY_DETAIL_TAB'; break;
                case 'contacts' : placement =  'CRM_CONTACT_DETAIL_TAB'; break;
                case 'quotes': placement = 'CRM_QUOTE_DETAIL_TAB'; break;
                case 'invoices' : placement = 'CRM_SMART_INVOICE_DETAIL_TAB'; break;
                }
            }
            
            console.log(placement)
            if(targetbtn.targetType === 'smart'){
                const res = await callMethod('crm.type.list')
                const types = res.data().types
                const IDs = types.map((type)=>type.entityTypeId)
                if(targetbtn.isOn){
                    IDs.forEach(async id=>await callMethod('placement.bind',{
                        PLACEMENT: `CRM_DYNAMIC_${id}_DETAIL_TAB`,
                        HANDLER: domain,
                        TITLE: ' ', 
                        DESCRIPTION: ' ', 
                    }))
                } else {
                    IDs.forEach(async id=>await callMethod('placement.unbind',{
                        PLACEMENT: `CRM_DYNAMIC_${id}_DETAIL_TAB`,
                        HANDLER: domain
                    }))
                }
            } else {
                !targetbtn?.isOn  
                ? 
                    await callMethod('placement.unbind', { 
                        PLACEMENT: placement, 
                        HANDLER: `${domain}`, 
                    })
                : 
                    await callMethod('placement.bind' , { 
                        PLACEMENT: placement, 
                        HANDLER: `${domain}`, 
                        TITLE: ' ', 
                        DESCRIPTION: ' ', 
                    });   
            }
            btnArray.value[index].isOn = !btnArray.value[index].isOn
            notification.open({
                message: targetbtn?.isOn 
                ? `''    ${notifyMessage(!targetbtn?.isOn, targetbtn?.targetType )}` 
                : `''    ${notifyMessage(!targetbtn?.isOn, targetbtn?.targetType )}`
                })
                
    }
</script>
<template>
    <div id="init">
        <a-typography-title  :level="2" class="title"> " "</a-typography-title>
        <a-typography-paragraph class="title">          "", </a-typography-paragraph> <br/>
        <a-typography-paragraph class="title">        ,    ,   
                 </a-typography-paragraph>
        <div class='container_btn' >
            <ActionButton v-for="btn in btnArray" :key="btn.targetType" :targetType="btn.targetType" :isOn="btn.isOn" @clickHandler="clickHandler" :tarType="btn.targetType"/>
        </div>
    </div>
</template>
<style scoped>
html, body {
        overflow: hidden;
    }
    #init{
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 100vw;
        height: 90vh;
    }
    .ui-btn-pad {
        width: 220px;
        margin-top: 5px;
    }
    .ui-btn {
        margin-left: 0 !important;
    }
    .title {
        text-align: center;
    }
    .container_btn {
        align-self: center;
        display: flex;
        flex-direction: column;
        width: fit-content !important;
        margin: 0% !important;
    }
    .container_btn > *{
        width: 20rem;
        margin-bottom: .5rem;
    }
</style>