<script setup>
import { DxTextBox } from 'devextreme-vue';
import cloneDeep from 'lodash.clonedeep'
import { CloseOutlined } from '@ant-design/icons-vue';
import { ref, onBeforeMount } from 'vue';
const props = defineProps(['row'])
const emit = defineEmits(['changePending'])
const textArr = ref([''])
onBeforeMount(()=>{
textArr.value = cloneDeep(props.row.TRUEVALUE)
})
const handleChange = (e, index) =>{
    console.log(e)
    textArr.value[index] = e.event.currentTarget?.value
    emit('changePending', textArr.value, textArr.value.join(', '))
}
const addField = () =>{
    textArr.value.push('')
}
const delField = (index) =>{
    textArr.value.splice(index,1)
    emit('changePending', textArr.value, textArr.value.join(', '))
}
</script>

<template>
<div v-for="(item, index) in textArr"   >

    <div style="display:flex; flex-direction:row; align-items: center;">
        <DxTextBox  :value="item" @input="handleChange($event, index)" />
        <CloseOutlined @click="delField(index)"/>
    </div>
    <br/>
</div>

<div style="display:flex; flex-direction:row;">
    <a-button style="margin-right: 0.5rem;" type="primary" @click="addField">
     
    </a-button>
</div>
</template>