<script setup>
import { callMethod } from "bx24-api";
import { onMounted, ref } from "vue";
import DxDropDownBox from "devextreme-vue/drop-down-box";
import DxTreeView from "devextreme-vue/tree-view";
import _ from "lodash";
const props = defineProps(["row", "valueRef"]);
const cachedBox = ref([]);
const row = ref({});
const selectArray = ref([]);
const value = ref({});
const isOpen = ref(false);
const visibleVal = ref(0);
const emit = defineEmits(["changePending"]);

const handleInput = async (e) => {
  console.log(e);
  switch (row.value.TYPE) {
    case "iblock_section": {
      const iblockid = row.value.IBLOCKID;
      const type = await (
        await callMethod(`lists.get.iblock.type.id`, { IBLOCK_ID: iblockid })
      ).data();
      const params = {
        IBLOCK_ID: iblockid,
        IBLOCK_TYPE_ID: type,
        FILTER: {
          "%NAME": e.event.currentTarget.value,
        },
      };
      selectArray.value = await (
        await callMethod("lists.section.get", params)
      )
        .data()
        .map((el) => ({
          ID: el.ID,
          NAME: `${el.NAME} [${el.ID}]`,
        }))
        .map((el) => {
          if (row.value.TRUEVALUE && row.value.TRUEVALUE.ID == el.ID) {
            return {
              ...el,
              CHECK: true,
            };
          } else
            return {
              ...el,
            };
        });
      if (row.value.TRUEVALUE) {
        value.value = _.cloneDeep(row.value.TRUEVALUE.ID);
      }
      cachedBox.value = _.cloneDeep(selectArray.value);
      break;
    }
    case "crm_contact": {
      selectArray.value = await (
        await callMethod("crm.contact.list", {
          FILTER: { "%TITLE": e.event.currentTarget.value },
        })
      )
        .data()
        .map((el) => ({
          ID: el.ID,
          NAME: el.NAME,
        }))
        .map((el) => {
          if (row.value.TRUEVALUE && row.value.TRUEVALUE.ID == el.ID) {
            return {
              ...el,
              CHECK: true,
            };
          } else
            return {
              ...el,
            };
        });
      cachedBox.value = _.cloneDeep(selectArray.value);
      if (row.value.TRUEVALUE) {
        value.value = _.cloneDeep(row.value.TRUEVALUE).ID;
      }
      break;
    }
    case "crm_company": {
      selectArray.value = await (
        await callMethod("crm.company.list", {
          FILTER: { "%TITLE": e.event.currentTarget.value },
        })
      )
        .data()
        .map((el) => ({
          ID: el.ID,
          NAME: el.TITLE,
        }))
        .map((el) => {
          if (row.value.TRUEVALUE && row.value.TRUEVALUE.ID == el.ID) {
            return {
              ...el,
              CHECK: true,
            };
          } else
            return {
              ...el,
            };
        });
      if (row.value.TRUEVALUE) {
        value.value = _.cloneDeep(row.value.TRUEVALUE).ID;
      }
      cachedBox.value = _.cloneDeep(selectArray.value);
      break;
    }
    case "iblock_element": {
      const iblockid = row.value.IBLOCKID;
      const type = await (
        await callMethod(`lists.get.iblock.type.id`, { IBLOCK_ID: iblockid })
      ).data();
      const params = {
        IBLOCK_ID: iblockid,
        IBLOCK_TYPE_ID: type,
        FILTER: { SEARCHABLE_CONTENT: `%${e.event.currentTarget.value}%` },
      };
      selectArray.value = await (
        await callMethod("lists.element.get", params)
      )
        .data()
        .map((el) => ({
          ID: el.ID,
          NAME: `${el.NAME} [${el.ID}]`,
        }))
        .map((el) => {
          if (row.value.TRUEVALUE.ID == el.ID) {
            return {
              ...el,
              CHECK: true,
            };
          } else
            return {
              ...el,
            };
        });
      break;
    }
    case "user":
    case "employee": {
      selectArray.value = await (
        await callMethod("user.search", {
          FIND: `${e.event.currentTarget.value}`,
        })
      )
        .data()
        .map((user) => ({
          ID: user.ID,
          NAME:
            user.NAME !== "" ? `${user.NAME} ${user.LAST_NAME}` : user.EMAIL,
        }))
        .map((el) => {
          if (row.value.TRUEVALUE.ID == el.ID) {
            return {
              ...el,
              CHECK: true,
            };
          } else
            return {
              ...el,
            };
        });
      break;
    }
  }
};
const handleChange = () => {
  document
    .getElementsByClassName("dx-texteditor-input")[0]
    .addEventListener("input", handleInput);
};
const setSelectedValue = (e) => {
  console.log(e);
  console.log(selectArray.value);
  const val = selectArray.value.find((el) => el.ID == e.itemData.ID);
  console.log(val);
  console.log(val.NAME);
  const display = val.NAME;
  emit("changePending", val, display);
  isOpen.value = false;
};
const updateView = (e, b) => {
  visibleVal.value = e?.component?.getSelectedNodeKeys() || b;
  console.log(visibleVal);
  isOpen.value = false;
};
onMounted(async () => {
  row.value = _.cloneDeep(props.row);

  switch (row.value.TYPE) {
    case "char": {
      selectArray.value = [
        {
          ID: "Y",
          NAME: "Y",
        },
        {
          ID: "N",
          NAME: "N",
        },
      ];
      break;
    }
    case "crm_contact": {
      selectArray.value = await (
        await callMethod("crm.contact.list")
      )
        .data()
        .map((el) => ({
          ID: el.ID,
          NAME: el.NAME,
        }))
        .map((el) => {
          if (row.value.TRUEVALUE && row.value.TRUEVALUE.ID == el.ID) {
            return {
              ...el,
              CHECK: true,
            };
          } else
            return {
              ...el,
            };
        });
      cachedBox.value = _.cloneDeep(selectArray.value);
      if (row.value.TRUEVALUE) {
        value.value = _.cloneDeep(row.value.TRUEVALUE).ID;
      }
      break;
    }
    case "crm_currency": {
      selectArray.value = await (
        await callMethod("crm.currency.list")
      )
        .data()
        .map((el) => ({
          ID: el.CURRENCY,
          NAME: el.CURRENCY,
        }))
        .map((el) => {
          if (row.value.TRUEVALUE && row.value.TRUEVALUE.ID == el.ID) {
            return {
              ...el,
              CHECK: true,
            };
          } else
            return {
              ...el,
            };
        });
      cachedBox.value = _.cloneDeep(selectArray.value);
      if (row.value.TRUEVALUE) {
        value.value = _.cloneDeep(row.value.TRUEVALUE).ID;
      }
      break;
    }
    case "crm_company": {
      selectArray.value = await (
        await callMethod("crm.company.list")
      )
        .data()
        .map((el) => ({
          ID: el.ID,
          NAME: el.TITLE,
        }))
        .map((el) => {
          if (row.value.TRUEVALUE && row.value.TRUEVALUE.ID == el.ID) {
            return {
              ...el,
              CHECK: true,
            };
          } else
            return {
              ...el,
            };
        });
      if (row.value.TRUEVALUE) {
        value.value = _.cloneDeep(row.value.TRUEVALUE).ID;
      }
      cachedBox.value = _.cloneDeep(selectArray.value);
      break;
    }
    case "iblock_section": {
      const iblockid = row.value.IBLOCKID;
      const type = await (
        await callMethod(`lists.get.iblock.type.id`, { IBLOCK_ID: iblockid })
      ).data();
      const params = { IBLOCK_ID: iblockid, IBLOCK_TYPE_ID: type };
      selectArray.value = await (
        await callMethod("lists.section.get", params)
      )
        .data()
        .map((el) => ({
          ID: el.ID,
          NAME: `${el.NAME} [${el.ID}]`,
        }))
        .map((el) => {
          if (row.value.TRUEVALUE && row.value.TRUEVALUE.ID == el.ID) {
            return {
              ...el,
              CHECK: true,
            };
          } else
            return {
              ...el,
            };
        });
      if (row.value.TRUEVALUE) {
        value.value = _.cloneDeep(row.value.TRUEVALUE.ID);
      }
      cachedBox.value = _.cloneDeep(selectArray.value);
      break;
    }
    case "crm_status": {
      let res = await (await callMethod("crm.status.list"))
        .data()
        .map((stat) => ({ ID: stat.STATUS_ID, NAME: stat.NAME }));
        if (row.value.TRUEVALUE) value.value = _.cloneDeep(row.value.TRUEVALUE.ID);
        console.log(row.value.TRUEVALUE);
        let IDs = Array.from(new Set(res.map((el) => el.ID)));
        IDs.forEach((id) => {
          selectArray.value = [...selectArray.value,  res.find((el) => el.ID == id)];
        });
        console.log(res.map((el) => el.ID).length);
        console.log(Array.from(new Set(res.map((el) => el.ID))).length);
        updateView(null, row.value.TRUEVALUE.ID);
      break;
    }
    case "boolean": {
      selectArray.value = [
        {
          ID: 0,
          NAME: "",
        },
        {
          ID: 1,
          NAME: "",
        },
      ];
      if (row.value.TRUEVALUE) {
        value.value = _.cloneDeep(row.value.TRUEVALUE.ID);
      }
      break;
    }
    case "iblock_element": {
      const iblockid = row.value.IBLOCKID;
      const type = await (
        await callMethod(`lists.get.iblock.type.id`, { IBLOCK_ID: iblockid })
      ).data();
      const params = { IBLOCK_ID: iblockid, IBLOCK_TYPE_ID: type };
      selectArray.value = await (
        await callMethod("lists.element.get", params)
      )
        .data()
        .map((el) => ({
          ID: el.ID,
          NAME: `${el.NAME} [${el.ID}]`,
        }))
        .map((el) => {
          if (row.value.TRUEVALUE.ID == el.ID) {
            return {
              ...el,
              CHECK: true,
            };
          } else
            return {
              ...el,
            };
        });
      if (row.value.TRUEVALUE) {
        value.value = _.cloneDeep(row.value.TRUEVALUE.ID);
      }
      cachedBox.value = _.cloneDeep(selectArray.value);
      break;
    }
    case "user":
    case "employee": {
      const id = row.value.TRUEVALUE.ID;
      const res1 = await (
        await callMethod("user.get", { ID: id })
      ).data()
      const res2 = await (
        (await callMethod("user.get"))
      ).data()
      const result = [...res1, ...res2]
      const res = Array.from(new Set([...res1.map(el=>el.ID), ...res2.map(el=>el.ID)]))
      const arr = []
      res.forEach(id=>{
        arr.push(result.find((el)=>el.ID==id))
      })
      selectArray.value = arr
        .map((user) => ({
          ID: user.ID,
          NAME:
            user.NAME !== "" ? `${user.NAME} ${user.LAST_NAME}` : user.EMAIL,
        }))
        .map((el) => {
          if (row.value.TRUEVALUE.ID == el.ID) {
            return {
              ...el,
              CHECK: true,
            };
          } else
            return {
              ...el,
            };
        });
      cachedBox.value = _.cloneDeep(selectArray.value);
      if (row.value.TRUEVALUE) {
        value.value = _.cloneDeep(row.value.TRUEVALUE.ID);
        updateView(null, row.value.TRUEVALUE.ID);
      }
      break;
    }
    case "enumeration": {
      selectArray.value = _.cloneDeep(
        row.value.ENUM.map((el) => ({
          ID: el.ID,
          NAME: el.VALUE,
        }))
      );
      console.log(row.value);
      if (row.value.TRUEVALUE) {
        value.value = _.cloneDeep(row.value.TRUEVALUE.ID);
      }
      break;
    }
  }
});
const clear = () => {
  emit("changePending", "", "");
  visibleVal.value = null
};
</script>
<template>
  <a-button @click="clear">  </a-button>
  <br />
  <DxDropDownBox
    :value="visibleVal"
    @opened="handleChange"
    :opened="isOpen"
    value-expr="ID"
    display-expr="NAME"
    :data-source="selectArray"
  >
    <DxTreeView
      :data-source="selectArray"
      :select-by-click="true"
      :search-enabled="true"
      selection-mode="single"
      :opened="isOpen"
      key-expr="ID"
      @item-selection-changed="updateView($event)"
      display-expr="NAME"
      search-expr="NAME"
      :search-editor-options="{
        onInput: _.debounce(async ($event) => await handleInput($event), 500),
      }"
      @item-click="setSelectedValue"
      :value="visibleVal"
    />
  </DxDropDownBox>
</template>
