<script setup>
import { callMethod } from "bx24-api";
import { ref, onBeforeMount } from "vue";
import DxDropDownBox from "devextreme-vue/drop-down-box";
import DxTreeView from "devextreme-vue/tree-view";
import _ from 'lodash'

const selectArray = ref({});
const value = ref({});
const entity = ref([]);
const visibleVal = ref({
    deal: null,
    lead: null,
    contact: null,
    company: null,
    invoice: null,
})
const props = defineProps(["row"]);
const emit = defineEmits("changePending");
const isOpened = ref({
    deal: false,
    lead: false,
    contact: false,
    company: false,
    invoice: false,
})
onBeforeMount(async () => {
  value.value = {
    deal: [],
    lead: [],
    contact: [],
    company: [],
    invoice: [],
  };
  const enitities = {
    'SI': 'invoice',
    'CO': 'company',
    'C_': 'contact',
    'D_': 'deal',
    'L_': 'lead',   
  }
  const getID = (e, v) =>{
    switch(e){
        case 'invoice': return v.substring(3)
        case 'company': return v.substring(3)
        case 'contact': return v.substring(2)
        case 'deal': return v.substring(2)
        case 'lead': return v.substring(2)
    }
  }
  entity.value = _.cloneDeep(props.row.CRMENTITY);
  if (!props.row.TRUEVALUE) return;
  const val = props.row.TRUEVALUE[0]
  const entit = enitities[val.substring(0,2)]
  const id = getID(entit, val)
  
  console.log(value.value)
  if(entit==='invoice'){
    selectArray.value[entit] = await (
        await callMethod(`crm.item.list`, {
          entityTypeId: 7,
        })
      )
        .data()
        .items.map((el) => ({
          ID: el.id,
          NAME: getName(entit, el),
        }));
  } else {
    selectArray.value[entit] = await (await callMethod(`crm.${entit}.list`))
      .data()
      .map((el) => ({
        ID: el.ID,
        NAME: getName(entit, el),
      }));
  }
  value.value[entit] = {
    ID: id
  }
  visibleVal.value[entit] = id
  
});
const handleChange = async (ev) => {
    isOpened.value[ev] = true
    console.log(ev)
  document
    .getElementsByClassName("dx-texteditor-input")[0]
    .addEventListener("input", (e) => handleInput(e, ev));
  if (ev === "invoice") {
    selectArray.value[ev] = await (
      await callMethod(`crm.item.list`, {
        entityTypeId: 7,
      })
    )
      .data()
      .items.map((elem) => ({
        ID: elem.id,
        NAME: getName(ev, elem),
      }));
    return;
  }
  selectArray.value[ev] = await (await callMethod(`crm.${ev}.list`))
    .data()
    .map((elem) => ({
      ID: elem.ID,
      NAME: getName(ev, elem),
    }));
};
const setSelectedValue = (e, ev) => {
    isOpened.value[ev] = false
  console.log(selectArray.value[ev], e);
  const val = e.itemData.ID;
  value.value[ev] = selectArray.value[ev].find((el) => el.ID == val);
  Object.keys(value.value).forEach((key) => {
    if (key == ev) return;
    else value.value[key] = null;
  });
  if (!e.itemData.selected) {
    value.value[ev] = e.itemData;
  } else {
    value.value[ev] = {}
  }
  console.log(value.value);
  const pend = Object.keys(value.value)
    .map((el) => {
      switch (el) {
        case "deal": {
          if (value.value[el]?.ID) return `D_${value.value[el].ID}`;
          else return ''
        }
        case "lead": {
          if (value.value[el]?.ID) return `L_${value.value[el].ID}`;
          else return ''
        }
        case "contact": {
          if (value.value[el]?.ID) return `C_${value.value[el].ID}`;
          else return ''
        }
        case "company": {
          if (value.value[el]?.ID) return `CO_${value.value[el].ID}`;
          else return ''
        }
        case "invoice": {
          if (value.value[el]?.ID) return `SI_${value.value[el].ID}`;
          else return ''
        }
      }
    })
    .filter((el) => el);
  const display = Object.keys(value.value)
    .map((el) => {
      switch (el) {
        case "deal": {
          if (value.value[el]) return `: ${value.value[el].NAME}`;
        }
        case "lead": {
          if (value.value[el]) return `: ${value.value[el].NAME}`;
        }
        case "contact": {
          if (value.value[el]) return `: ${value.value[el].NAME}`;
        }
        case "company": {
          if (value.value[el]) return `: ${value.value[el].NAME}`;
        }
        case "invoice": {
          if (value.value[el]) return `: ${value.value[el].NAME}`;
        }
      }
    })
    .filter((el) => el)
    .join(", ");
  emit("changePending", pend, display);
  isOpened.value[ev] = false
};
const getName = (entity, obj) => {
  switch (entity) {
    case "lead": {

     return `${obj.TITLE} [${obj.ID}]`;
    }
    case "deal":
      return `${obj.TITLE} [${obj.ID}]`;
    case "contact":
      return `${obj.NAME} [${obj.ID}]`;
    case "company":
      return `${obj.TITLE} [${obj.ID}]`;
    case "invoice":
      return `${obj.searchContent} [${obj.id}]`;
  }
};
const handleInput = async (e, ev) => {
  if (ev === "invoice"){
    const invoiceparams = {
        entityTypeId: 7,
        filter: {
          "%searchContent": e.event.currentTarget.value,
        },
      };
      await (
        await callMethod(`crm.item.list`, invoiceparams)
      )
        .data()
        .items.map((el) => ({
          ID: el.id,
          NAME: `${el.searchContent} [${el.id}]`,
        }))
  }
  else {
    await (
      await callMethod(`crm.${ev}.list`, {
        FILTER: { "%TITLE": `${e.event.currentTarget.value}` },
      })
    )
      .data()
      .map((elem) => ({
        ID: elem.ID,
        NAME: getName(ev, elem),
      }))
      .forEach((elem) => {
        if (selectArray.value[ev].filter((el) => el.ID == elem.ID).length)
          return;
        else selectArray.value[ev] = [...selectArray.value[ev], elem];
      });
  }
};
const updateView = (e, ent) =>{
    Object.keys(visibleVal.value).forEach((key) => {
    if (key == ent) return;
    else visibleVal.value[key] = null;
  });
    visibleVal.value[ent] = e.component.getSelectedNodeKeys()
    console.log(visibleVal.value)
    isOpened.value[ent] = false
}
const clear = () =>{
    value.value = {}
    visibleVal.value = {}
    emit('changePending', '', '')
}
</script>
<template>
  <div style="overflow-y: visible">
    <a-button @click="clear">  </a-button>
    <br/>
    <div
      v-if="entity.includes('CONTACT')"
      style="
        display: grid;
        grid-template-rows: 2rem;
        grid-template-columns: repeat(2, 5rem, 10rem);
        padding-bottom: 1rem;
      "
    >
      <h3></h3>
      <DxDropDownBox
      :value="visibleVal['contact']"
      :data-source="selectArray['contact']"
      value-expr="ID"
      display-expr="NAME"
      @opened="handleChange('contact')"  
      :opened="isOpened['contact']"
    >
      <DxTreeView
        @opened="handleChange('contact')"
        ref="drop"
        :value="value['contact']?.ID"
        data-structure="plain"
        selection-mode="single"
        @item-click="setSelectedValue($event, 'contact')"
        :search-enabled="true"
        @item-selection-changed="updateView($event, 'contact')"
        :select-by-click="true"
        key-expr="ID"
        display-expr="NAME"
        search-expr="NAME"
        :data-source="selectArray['contact']"
        :search-editor-options="{
          onInput:_.debounce( async ($event) =>
            await handleInput($event, 'contact'),500),
        }"
      />
    </DxDropDownBox>
    </div>
    <div
      v-if="entity.includes('COMPANY')"
      style="
        display: grid;
        grid-template-rows: 2rem;
        grid-template-columns: repeat(2, 5rem, 10rem);
        padding-bottom: 1rem;
      "
    >
      <h3></h3>
      <DxDropDownBox
      :data-source="selectArray['company']"
      value-expr="ID"
      display-expr="NAME"
      @opened="handleChange('company')"  
      :opened="isOpened['company']"
      :value="visibleVal['company']"
    >
      <DxTreeView
        @opened="handleChange('company')"
        ref="drop"
        :value="visibleVal['company']?.ID"
        data-structure="plain"
        selection-mode="single"
        @item-selection-changed="updateView($event, 'company')"
        @item-click="setSelectedValue($event, 'company')"
        :search-enabled="true"
        :select-by-click="true"
        key-expr="ID"
        display-expr="NAME"
        search-expr="NAME"
        :data-source="selectArray['company']"
        :search-editor-options="{
          onInput:_.debounce(async ($event) =>
            await handleInput($event, 'company'),500),
        }"
      />
    </DxDropDownBox>
    </div>
    <div
      v-if="entity.includes('LEAD')"
      style="display: grid;
        grid-template-rows: 2rem;
        grid-template-columns: repeat(2, 5rem, 10rem);
        padding-bottom: 1rem;
      "
    >
      <h3></h3>
      <DxDropDownBox
      :value="visibleVal['lead']"
      :data-source="selectArray['lead']"
      value-expr="ID"
      display-expr="NAME"
      @opened="handleChange('lead')"  
      :opened="isOpened['lead']"
    >
      <DxTreeView
        @opened="handleChange('lead')"
        ref="drop"
        :value="value['lead']?.ID"
        data-structure="plain"
        @item-selection-changed="updateView($event, 'lead')"
        selection-mode="single"
        @item-click="setSelectedValue($event, 'lead')"
        :search-enabled="true"
        :select-by-click="true"
        key-expr="ID"
        display-expr="NAME"
        search-expr="NAME"
        :data-source="selectArray['lead']"
        :search-editor-options="{
          onInput:_.debounce( async ($event) =>
            await handleInput($event, 'lead'), 500),
        }"
      />
    </DxDropDownBox>
    </div>
    <div
      v-if="entity.includes('SMART_INVOICE')"
      style="display: grid;
        grid-template-rows: 2rem;
        grid-template-columns: repeat(2, 5rem, 10rem);
        padding-bottom: 1rem;
      "
    >
      <h3></h3>
      <DxDropDownBox
      :value="visibleVal['invoice']"
      :data-source="selectArray['invoice']"
      value-expr="ID"
      display-expr="NAME"
      @opened="handleChange('invoice')"  
      :opened="isOpened['invoice']"
    >
      <DxTreeView
        @opened="handleChange('invoice')"
        ref="drop"
        :value="value['invoice']?.ID"
        @item-selection-changed="updateView($event, 'invoice')"

        data-structure="plain"
        selection-mode="single"
        @item-click="setSelectedValue($event, 'invoice')"
        :search-enabled="true"
        :select-by-click="true"
        key-expr="ID"
        display-expr="NAME"
        search-expr="NAME"
        :data-source="selectArray['invoice']"
        :search-editor-options="{
          onInput: _.debounce(async ($event) =>
            await handleInput($event, 'invoice'),500),
        }"
      />
    </DxDropDownBox>
    </div>
    <div
      v-if="entity.includes('DEAL')"
      style=" display: grid;
        grid-template-rows: 2rem;
        grid-template-columns: repeat(2, 5rem, 10rem);
        padding-bottom: 1rem;
      "
    >
      <h3></h3>
      <DxDropDownBox
      :data-source="selectArray['deal']"
      value-expr="ID"
      display-expr="NAME"
      @opened="handleChange('deal')"  
      :value="visibleVal['deal']"
      :opened="isOpened['deal']"
    >
      <DxTreeView
        @opened="handleChange('deal')"
        ref="drop"
        :value="value['deal']?.ID"
        data-structure="plain"
        selection-mode="single"
        @item-selection-changed="updateView($event, 'deal')"
        @item-click="setSelectedValue($event, 'deal')"
        :search-enabled="true"
        :select-by-click="true"
        key-expr="ID"
        display-expr="NAME"
        search-expr="NAME"
        :data-source="selectArray['deal']"
        :search-editor-options="{
          onInput:_.debounce( async ($event) =>
            await  handleInput($event, 'deal'), 500),
        }"
      />
    </DxDropDownBox>
    </div>
  </div>
</template>
